import { useEffect, useState, useMemo } from "react";
import  ObjectEntity  from "../../models/ObjectEntity";
import { getObjects} from "../../services/objectService";

// Для таблицы
import * as React from 'react';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Button, IconButton, Box, Switch, FormControlLabel, Tooltip, CircularProgress, Typography } from "@mui/material";
import { TableVirtuoso, TableComponents } from 'react-virtuoso';
import { TableSortLabel } from '@mui/material';
import UnfoldLessIcon from '@mui/icons-material/UnfoldLess';
import UnfoldMoreIcon from '@mui/icons-material/UnfoldMore';
import { useLocation, useNavigate } from "react-router-dom";
import StatusMessage from "../../components/StatusMessage";
import { getObjectsWithMaterials} from "../../services/materialService";
import { stableSort, getComparator, VirtuosoTableComponents } from "../../utils/sort-tables";


interface ColumnData {
  dataKey: keyof ObjectEntity | "actions";
  label: string;
  numeric?: boolean;
  width?: number;
  sortable?: boolean;
}
type Order = 'asc' | 'desc';

const columns: ColumnData[] = [
  {
    width: 10,
    label: 'ID',
    dataKey: 'nOb',
    numeric: true,
    sortable: true
  },
  {
    width: 150,
    label: 'Название объекта',
    dataKey: 'nameOfObject',
    sortable: true
  },
  {
    width: 50,
    label: 'Материалы',
    dataKey: "actions",
    sortable: false
  },
];

// Генерация контента в таблице
const RowContent: React.FC<{ row: ObjectEntity; objectsWithMaterialsIds: number[] }> = ({ row, objectsWithMaterialsIds }) => {
  const navigate = useNavigate();
  const location = useLocation();


  // Проверяем, есть ли материалы у объекта
  const hasMaterials = objectsWithMaterialsIds.includes(row.nOb);

  return (
    <>
      {columns.map((column) => {
        if (column.dataKey === "actions") {
          return (
            <TableCell key="actions" align="center">
              <Button
                variant="contained"
                size="small"
                onClick={() =>
                  navigate(`/materials/${row.nOb}`, { state: { 
                    background: location,
                    nameOfObject: row.nameOfObject
                   } })}

                sx={{
                  backgroundColor: hasMaterials ? 'primary.main' : '#f44336', // красный если нет материалов
                  '&:hover': {
                    backgroundColor: hasMaterials ? 'primary.dark' : '#d32f2f', // темно-красный при наведении
                  },
                  minWidth: '40px'
                }}
              >
                📦🏗️
              </Button>
            </TableCell>
          );
        }
        return (
          <TableCell
            key={column.dataKey}
            align={column.numeric || false ? "right" : "left"}
            onDoubleClick={() =>
                  navigate(`/rabota/${row.nOb}`, { 
                    state: { 
                        objectName: row.nameOfObject 
                    } 
                } )}
            style={{ cursor: 'pointer' }}
          >
            {row[column.dataKey as keyof ObjectEntity]}
          </TableCell>
        );
      })}
    </>
  );
};



// "Шапка таблицы"
interface EnhancedTableHeadProps {
  onRequestSort: (event: React.MouseEvent<unknown>, property: keyof ObjectEntity) => void;
  order: Order;
  orderBy: string;
  onToggleCompact: () => void;
  isCompact: boolean;
}

function EnhancedTableHead(props: EnhancedTableHeadProps) {
  const { order, orderBy, onRequestSort, onToggleCompact, isCompact } = props;
  
  const createSortHandler =
    (property: keyof ObjectEntity) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <TableRow>
      {columns.map((column) => {
        if (column.dataKey === "actions") {
          return (
            <TableCell
              key={column.dataKey}
              variant="head"
              align="center"
              style={{ width: column.width }}
              sx={{ backgroundColor: "background.paper" }}
            >
              <Box display="flex" alignItems="center" justifyContent="center">
                {column.label}
                <Tooltip title={isCompact ? "Расширить" : "Сжать"}>
                  <IconButton 
                    size="small" 
                    onClick={onToggleCompact}
                    sx={{ ml: 1 }}
                  >
                    {isCompact ? <UnfoldMoreIcon /> : <UnfoldLessIcon />}
                  </IconButton>
                </Tooltip>
              </Box>
            </TableCell>
          );
        }

        return (
          <TableCell
            key={column.dataKey}
            variant="head"
            align={column.numeric || false ? "right" : "left"}
            style={{ width: column.width }}
            sx={{ backgroundColor: "background.paper" }}
          >
            {column.sortable ? (
              <TableSortLabel
                active={orderBy === column.dataKey}
                direction={orderBy === column.dataKey ? order : 'asc'}
                onClick={createSortHandler(column.dataKey as keyof ObjectEntity)}
              >
                {column.label}
              </TableSortLabel>
            ) : (
              column.label
            )}
          </TableCell>
        );
      })}
    </TableRow>
  );
}





function MainPage() {
    
  const [objects, setObjects] = useState<ObjectEntity[]>([]);
  const [loading, setLoading] = useState(true);
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<keyof ObjectEntity>('nameOfObject');
  const [isCompact, setIsCompact] = useState(false);
  const [objectsWithMaterialsIds, setObjectsWithMaterialsIds] = useState<number[]>([]);

  useEffect(() => {
  const fetchObjectsWithMaterials = async () => {
    try {
      setLoading(true);
      
      // Получаем все объекты
      const allObjects = await getObjects("aalihacheva");
      setObjects(allObjects);
      
      // Параллельно получаем объекты с материалами
      const objectsWithMaterials = await getObjectsWithMaterials();
      setObjectsWithMaterialsIds(objectsWithMaterials);

    } catch (err) {
      console.error('Ошибка при загрузке объектов:', err);
    } finally {
      setLoading(false);
    }
  };

  fetchObjectsWithMaterials();
}, []);




  // Для сортировки
  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof ObjectEntity,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const sortedObjects = useMemo(() => {
    return stableSort(objects, getComparator(order, orderBy));
  }, [objects, order, orderBy]);


  // Для визуального упрощения (компактный режим)
  const handleToggleCompact = () => {
    setIsCompact(!isCompact);
  };

  const compactWidths = {
    nOb: 80,
    nameOfObject: 120,
    actions: 40
  };
  
  const currentColumns = useMemo(() => {
    return isCompact 
      ? columns.map(col => ({
          ...col,
          width: compactWidths[col.dataKey as keyof typeof compactWidths] || col.width
        }))
      : columns;
  }, [isCompact]);

  const tableHeight = useMemo(() => 
    isCompact ? "400px" : { xs: "100vh", md: "100vh", lg: "100vh" },
  [isCompact]);


 
  function fixedHeaderContent() {
    return (
      <EnhancedTableHead
        order={order}
        orderBy={orderBy}
        onRequestSort={handleRequestSort}
        onToggleCompact={handleToggleCompact}
        isCompact={isCompact}
      />
    );
  }

if (loading) {
        return (
            <StatusMessage>
              <CircularProgress />
                <Typography variant="h6" sx={{ ml: 2 }}>
                    Загрузка данных...
                </Typography>
            </StatusMessage>
        );
    }

  // Основной код страницы и то, что выводится на экран
  return (
    <Box sx={{ width: '100%'}}>
    
      <Paper sx={{
        height: tableHeight,
        maxWidth: "1400px",
        minWidth: "280px",
        margin: "0 auto"
      }}>
        <TableVirtuoso
          data={sortedObjects}
          components={VirtuosoTableComponents}
          fixedHeaderContent={fixedHeaderContent}
          itemContent={(_index, row) => <RowContent row={row} objectsWithMaterialsIds={objectsWithMaterialsIds} />}
        />
      </Paper>
    </Box>
  );
}

export default MainPage;