import API_BASE_URL from "../config";
import sprRabota from "../models/SprRabota";

const API_URL = API_BASE_URL+"/api/sprrabota";


export interface ISprRabota {
  count: number;
  data: sprRabota[];
}

export async function getSprRabota(): Promise<sprRabota[]> {
    const response = await fetch(`${API_URL}/getAll`);
    
    if (!response.ok){
        throw new Error("Не удалось получить сведения");
    }

    const result: ISprRabota = await response.json();
    return result.data || [];
}


export const fetchChildSprRabota = async (parentId: number) => {
  try {
    const response = await fetch(`${API_URL}/children/${parentId}`);
    if (!response.ok) throw new Error('Ошибка загрузки');
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Ошибка:', error);
    return [];
  }
};

export const fetchSprRabotaMelk = async (projectRabotaId: number) => {
  try {
    const response = await fetch(`${API_URL}/melk/${projectRabotaId}`);
    if (!response.ok) throw new Error('Ошибка загрузки');
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Ошибка:', error);
    return [];
  }
};


export const checkMelkRabotaExists = async (
  objectId: number, 
  SprRabotaKomplId: number, 
  rabotaPrim: string, 
  sekcii: number[]) => {
  try {
    for (const sekc of sekcii) {
      const response = await fetch(`${API_URL}/melkIsExist`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          objectId: objectId,
          SprRabotaKomplId: SprRabotaKomplId,
          rabotaPrim: rabotaPrim,
          sekc: sekc
        })
      });

      if (!response.ok) {
        throw new Error('Ошибка запроса');
      }

      const exists = await response.json();
      if (exists === true) {
        console.log(`Секция ${sekc}: запись уже существует`);
        return true; // останавливаем цикл и выходим
      }
    }
    return false;
  } catch (error) {
    console.error('Ошибка:', error);
    return false;
  }
};


export const addMelkRabota = async (
    objectId: number, 
    smetaId: number, 
    smeta: string, 
    sprRabota: string, 
    projectRabotaId: number, 
    rabotaPrim: string, 
    sekcii: number[],
    sprRabotaKompl: string,
    sprRabotaKomplId: number,
    edIzmId?: number, 
    edIzm?: string, 
    ) => 
{
  try {
      const requestBody: any = {
      objectId,
      smetaId,
      smeta,
      sprRabota,
      projectRabotaId,
      rabotaPrim,
      sekcii,
      sprRabotaKompl,
      sprRabotaKomplId,
      edIzmId,
      edIzm
    };
    console.log('Отправляем JSON:', requestBody);

    const response = await fetch(`${API_URL}/AddMelkRabota`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(requestBody),
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error('Ошибка запроса:', errorText);
      throw new Error(`Ошибка запроса: ${response.status}`);
    }

    const exists = await response.json();
    return exists; // true или false
  } catch (error) {
    console.error('Ошибка:', error);
    return false;
  }
};


export const deleteMelkRabota = async (
  objectId: number,
  projectRabotaId: number,
  rabotaPrim: string,
  sprRabotaKomplId?: number,
): Promise<boolean> => {
  try {
    const requestBody = { objectId, projectRabotaId, sprRabotaKomplId, rabotaPrim };
    console.log('requestBody=', requestBody);

    const response = await fetch(`${API_URL}/DeleteMelkRabota`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(requestBody),
    });

    console.log('response: ', response);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || "Failed to delete job");
    }

    const data = await response.json();
    console.log("Ответ сервера:", data);

    return data === true || data === "true";
  } catch (error) {
    console.error("Ошибка:", error);
    return false;
  }
};
