// services/httpService.ts
const API_URL = "http://localhost:5189/api/Rabota";
// Функция для выполнения UPDATE_ObjemObsh
export async function updateObjemObsh(objectId: number): Promise<boolean> {
  try {
    console.log('Выполнение процедур при закрытии формы для объекта:', objectId);
    const response = await fetch(`${API_URL}/update-objem-obsh`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ objectId }),
    });

    if (!response.ok) {
      throw new Error('Ошибка при обновлении общего объема');
    }

    const result = await response.json();
    return result.success;
    
  } catch (error) {
    console.error('Ошибка в updateObjemObsh:', error);
    return false;
  }
}

