import { TableContainer, Paper, Table, TableHead, TableRow, TableBody } from "@mui/material";
import React, { useMemo } from "react";
import { TableComponents } from "react-virtuoso";
import ObjectEntity from "../models/ObjectEntity";


export interface ColumnData {
  dataKey: keyof ObjectEntity | "actions";
  label: string;
  numeric?: boolean;
  width?: number;
  sortable?: boolean;
}

type Order = 'asc' | 'desc';

// Функции для сортировки
export const VirtuosoTableComponents: TableComponents<ObjectEntity> = {
  Scroller: React.forwardRef<HTMLDivElement>((props, ref) => (
    <TableContainer component={Paper} {...props} ref={ref} />
  )),
  Table: (props) => (
    <Table {...props} sx={{ borderCollapse: "separate", tableLayout: "fixed" }} />
  ),
  TableHead: React.forwardRef<HTMLTableSectionElement>((props, ref) => (
    <TableHead {...props} ref={ref} />
  )),
  TableRow,
  TableBody: React.forwardRef<HTMLTableSectionElement>((props, ref) => (
    <TableBody {...props} ref={ref} />
  )),
};

export function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
  const av = a[orderBy];
  const bv = b[orderBy];

  // Обработка undefined и null значений
  if (bv === undefined || bv === null) {
    return av === undefined || av === null ? 0 : -1;
  }
  if (av === undefined || av === null) {
    return 1;
  }

  // Для чисел и строк
  if (typeof bv === 'number' && typeof av === 'number') {
    return bv - av;
  }

  // Для строк и других типов
  const aString = String(av).toLowerCase();
  const bString = String(bv).toLowerCase();

  if (bString < aString) return -1;
  if (bString > aString) return 1;
  return 0;
}

export function getComparator<T>(
  order: Order,
  orderBy: keyof T,
): (a: T, b: T) => number {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}


export function stableSort<T>(array: readonly T[], comparator: (a: T, b: T) => number) {
  const stabilizedThis = array.map((el, index) => [el, index] as [T, number]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}






