import { Navigate, Route, BrowserRouter as Router, Routes, useLocation, useNavigate, useParams } from 'react-router-dom';
import AuthPage from './pages/auth/auth-page';
import MainPage from './pages/main/main-page';
import Layout from './components/layout/layout';
import MaterialsDialog from './dialogs/materials/materials-dialod';
import RabotaPage from './pages/rabota/rabota-page';
import VyborForm from './dialogs/rabota/Vybor';
import Rabota from './models/Rabota';
import VyborMelkForm from './dialogs/rabota/vyborMelk';
import { useState, useEffect } from 'react';

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const checkAuth = () => {
      const userLogin = localStorage.getItem('userLogin');
      setIsAuthenticated(!!userLogin);
      setIsLoading(false);
    };

    checkAuth();
  }, []);

  

  return (
    <Router>
      <AppRoutes 
        isAuthenticated={isAuthenticated} 
        setIsAuthenticated={setIsAuthenticated} 
      />
    </Router>
  );
}

function AppRoutes({ isAuthenticated, setIsAuthenticated }: { 
  isAuthenticated: boolean; 
  setIsAuthenticated: (auth: boolean) => void;
}) {
  const location = useLocation();
  // для сохранения "фона" (страницы, с которой мы пришли) 
  const state = location.state as { background?: Location };

  return (
    <>
      <Routes>
        <Route 
          path="/auth" 
          element={
            isAuthenticated ? 
              <Navigate to="/" replace /> : 
              <AuthPage setIsAuthenticated={setIsAuthenticated} />
          } 
        />
        
        <Route path="/" element={isAuthenticated ? <Layout /> : <Navigate to="/auth" replace state={{ from: location }} />}>
          <Route index element={<MainPage />} />
          <Route path="materials/:objectId" element={<MainPage />} />
          <Route path="rabota/:objectId/vybor" element={<RabotaPage/>} />
          <Route path="rabota/:objectId/vyborMelk" element={<RabotaPage/>} />
          <Route path="rabota/:objectId" element={
              isAuthenticated ? <RabotaPage /> : <Navigate to="/auth" replace />
            } />
        </Route>
        
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>


      {/* Модалка поверх таблицы */}
      {state?.background && (
        <Routes>
          <Route path="materials/:objectId" element={<MaterialsDialogWrapper />} />
          <Route path="rabota/:objectId/vybor" element={<VyborFormWrapper />} />
          <Route path="rabota/:objectId/vyborMelk" element={<VyborMelkFormWrapper />} />
        </Routes>
      )}
    </>
  );
}

interface LocationState {
  background?: Location;
  nameOfObject?: string;
  sekcii?: number[];
  selectedSection?: number;
  selectedRabota?: Rabota;
}


function MaterialsDialogWrapper() {
  const location = useLocation();
  const state = location.state as LocationState;
  const nameOfObject = state?.nameOfObject;
  const { objectId } = useParams();
  
  const navigate = useNavigate();

  return (
    <MaterialsDialog
      objectId={Number(objectId)}
      open={true}
      nameOfObject= {nameOfObject}
      onClose={() => navigate(-1)}
    />
  );
}


function VyborFormWrapper() {
  const location = useLocation();
  const state = location.state as LocationState;
  const navigate = useNavigate();
  const { objectId } = useParams();
  const { sekcii } = useParams();

  return (
    <VyborForm
      open={true}
      onClose={() => navigate(-1)}
      objectId = {Number(objectId)}
      sekcii={state?.sekcii ?? []}
      selectedSection={state?.selectedSection ?? 1}
    />
  );
}

function VyborMelkFormWrapper() {
  const location = useLocation();
  const state = location.state as LocationState;
  const navigate = useNavigate();
  const { objectId } = useParams();
  const { sekcii } = useParams();
  const {selectedRabota} = useParams();


  return (
    <VyborMelkForm
      open={true}
      onClose={() => navigate(-1)}
      objectId = {Number(objectId)}
      sekcii={state?.sekcii ?? []}
      selectedSection={state?.selectedSection ?? 1}
      selectedRabota={state?.selectedRabota ?? null}
    />
  );
}


export default App;
