import { 
  Box, 
  Button, 
  Checkbox, 
  FormControlLabel, 
  FormControl,
  InputLabel, 
  MenuItem, 
  Select, 
  TextField, 
  Typography,
  Paper,
  Grid, 
  Stack,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton
} from '@mui/material';
import { IDopMaterialTableProps } from '../../interfaces/IDopMaterialTableProps';
import { useEffect, useState } from 'react';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import Rabota from '../../models/Rabota';
import MaterialTable from '../tables/materials-table';
import CloseIcon from '@mui/icons-material/Close';
import { getSprRzch } from '../../services/spravochnikService';
import SprRzch from '../../models/SprRzch';
import DopRab from '../../models/DopRab';
import { getDopRab, getFilteredDopRab } from '../../services/normativService';
import RubricatorDialog from '../../dialogs/materials/newObjem-dialog';
import { saveMaterialDop, saveToTmpMat, updateDopRab } from '../../services/materialService';
import sprMaterial, { SelectedMaterial } from '../../models/SprMaterial';
import { change_data, GetDopMaterialPoKrupnym } from '../../services/materialDopService';
import Material from '../../models/MaterialSummary';
import SprMaterial from '../../models/SprMaterial';


const MaterialDopDialog: React.FC<IDopMaterialTableProps & { 
  open: boolean; onClose: () => void }> = ({ 
  rabota: propData,
  nameOfObject,
  flagTipRab,
  open,
  onClose
}) => {

  // console.log('При загрузке flagTipRab=', flagTipRab); -- корректно
  const [selectedRabota, setSelectedRabota] = useState<Rabota>();
  const [dopRab, setDopRab] = useState<DopRab[]>([]);

  // Установка ответственного
  const [sprRzchList, setSprRzchList] = useState<SprRzch[]>([]);
  const [responsible, setResponsible] = useState('');

  //Установка даты
  const [availableDates, setAvailableDates] = useState<string[]>([]);
  const [selectedDate, setSelectedDate] = useState<string>('');


  const [obosn, setObosn] = useState<string>('');
  const [objemDop, setObjemDop] = useState<number>();

  const [isDataLoaded, setIsDataLoaded] = useState(false);

  const [krupnye, setKrupnye] = useState(false);
  const [poKrupnym, setPoKrupnym] = useState(false);

  const [selectedMaterials, setSelectedMaterials] = useState<sprMaterial[]>([]);

  const [isChangingResponsible, setIsChangingResponsible] = useState(false);

  ////////////////////////////////////////Загрузка данных при монтировании



  const loadSprRzch = async (propData: Rabota) => {
      try {
        // console.log('Началась загрузка ответственных');
        const sprRzch = await getSprRzch(propData.object_ID, propData.projectRabota_ID, propData.rabotaPrim);
        setSprRzchList(sprRzch);
        
      } catch (error) {
        console.error('Ошибка загрузки справочника:', error);
      }}

   const loadDopRab = async (propData: Rabota) => {
      try {
        console.log('Началась загрузка работ');
        const _dopRab = await getDopRab(propData.object_ID, propData.projectRabota_ID, propData.rabotaPrim);
        setDopRab(_dopRab);
        console.log('Загрузившиеся работы:', _dopRab);
      } catch (error) {
        console.error('Ошибка загрузки работы:', error);
      }}


  useEffect(() => {
    if (propData) {
      setSelectedRabota(propData);
      loadSprRzch(propData);
      loadDopRab(propData);

    };
 }, [propData]);

  const handleSelectedMaterialsSet = (selectedMaterials: sprMaterial[]) => {
    setSelectedMaterials(selectedMaterials);
  };


 useEffect(() => {
  setObjemDop(0);
  setObosn('');
  setSelectedDate('');
  if (responsible ) {

    let sprRabota_Kompl_ID = flagTipRab == 'Крупная' ? 0 : selectedRabota?.sprRabota_Kompl_ID;

     const filteredDates = dopRab
        .filter(item => item.sprRzch_ID.toString() == responsible
          && item.sprRabota_Kompl_ID === sprRabota_Kompl_ID && item.data)
        .map(item => {
          const date = new Date(item.data!);
          const day = date.getDate().toString().padStart(2, '0');
          const month = (date.getMonth() + 1).toString().padStart(2, '0');
          const year = date.getFullYear();
          const hours = date.getHours().toString().padStart(2, '0');
          const minutes = date.getMinutes().toString().padStart(2, '0');
          return `${day}.${month}.${year} ${hours}:${minutes}`;
        })
        .filter(dateString => dateString !== 'NaN.NaN.NaN NaN:NaN');
      if (filteredDates) {
        setAvailableDates(filteredDates);
        setSelectedDate(filteredDates[0]);
        console.log('filteredDates', filteredDates, 'selectedRabota?',selectedRabota, 'responsible=',responsible);
      }
      else {
        setAvailableDates([]);
        setSelectedDate('');
        console.log('filteredDates', filteredDates, 'selectedRabota?',selectedRabota)
      }
  }
 }, [responsible, dopRab, selectedRabota]);

useEffect(()=>{

  const getAllMaterials = async() => {
    if (poKrupnym===true && selectedRabota){
      let allDopMaterials = await GetDopMaterialPoKrupnym({
        ObjectId: selectedRabota.object_ID,
        ProjectRabotaId: selectedRabota.projectRabota_ID,
        RabotaPrim: selectedRabota.rabotaPrim
      })

      console.log('allDopMaterials при заведении крупных доп работ:', allDopMaterials)
      const converted: SprMaterial[] = allDopMaterials.map(m => ({
                sprMaterial_ID: m.sprMaterial_ID,
                material_ID: m.material_ID ?? 0,
                parent_ID: 0,
                isFolder: 0,
                edIzm: m.edIzm ?? '',
                sprEdizm_ID: m.edIzm_ID ?? 0,
                cena: m.cena ?? 0,
                sprMaterial: m.sprMaterial ?? '',
                objem: m.objem ?? 0,
                comment: m.comment ?? '',
                user: m.user ?? '',
                data: m.data ?? ''
              }));

      setSelectedMaterials(converted);
      
    }
  }
  getAllMaterials();

  if (poKrupnym===false){
    setResponsible('');
  }

},[poKrupnym, selectedRabota])


useEffect(() => {
  if (selectedDate && responsible && dopRab.length > 0 && selectedRabota) {

    const foundDopRab = dopRab.find(item => {
      const itemDate = new Date(item.data!);
      if (itemDate) {
        const formattedItemDate = `${itemDate.getDate().toString().padStart(2, '0')}.${(itemDate.getMonth() + 1).toString().padStart(2, '0')}.${itemDate.getFullYear()} ${itemDate.getHours().toString().padStart(2, '0')}:${itemDate.getMinutes().toString().padStart(2, '0')}`;
        
        const dateMatch = formattedItemDate === selectedDate.toString();
        const sprRzchMatch = item.sprRzch_ID.toString() === responsible.toString();
        const komplMatch = item.sprRabota_Kompl_ID === selectedRabota.sprRabota_Kompl_ID;

        return dateMatch && sprRzchMatch && komplMatch;
      }
      return false;
    });

    if (foundDopRab) {
      setObosn(foundDopRab.obosn || '');
      setObjemDop(foundDopRab.objemDop || 0);
      setIsDataLoaded(true);
    }
  }
}, [selectedDate]);

useEffect(() => {
  setIsDataLoaded(false);
}, [selectedDate, responsible]);

useEffect(() => {
  if (flagTipRab === 'Крупная') {
    setKrupnye(true);
  }
}, [flagTipRab]);



/////////////////////Функция для загрузки материалов//////////////////////////////////////
useEffect(() => {
  const loadMaterials = async () => {
    if (!selectedRabota || !selectedDate || isChangingResponsible) return;

    setIsDataLoaded(false);
    
    try {

      const loadedMaterials = await change_data({
        Object_ID: selectedRabota.object_ID, 
        ProjectRabota_ID: selectedRabota.projectRabota_ID,  
        RabotaPrim: selectedRabota?.rabotaPrim || ' ',
        SprRzch_ID: parseInt(responsible),
        data: selectedDate,
        flagTipRab: flagTipRab!,              
        SprRabota_Kompl_ID: selectedRabota?.sprRabota_Kompl_ID || 0, 
      });

      const converted: SprMaterial[] = loadedMaterials.map(m => ({
        sprMaterial_ID: m.sprMaterial_ID,
        material_ID: m.material_ID ?? 0,
        parent_ID: 0,
        isFolder: 0,
        edIzm: m.edIzm ?? '',
        sprEdizm_ID: m.edIzm_ID ?? 0,
        cena: m.cena ?? 0,
        sprMaterial: m.sprMaterial ?? '',
        objem: m.objem ?? 0,
        comment: m.comment ?? '',
        user: m.user ?? '',
        data: m.data ?? ''
      }));
      
      console.log('loadedMaterials:',converted);
      setSelectedMaterials(converted);

    } catch (error) {
      console.error('Ошибка при загрузке уже имеющихся материалов:', error);
    } finally {
      setIsDataLoaded(true);
    }
  };

  


  if (selectedDate && responsible && selectedRabota && !isChangingResponsible) {
  loadMaterials();
  
  }
}, [flagTipRab, selectedRabota, responsible, selectedDate, isChangingResponsible]);




useEffect (()=> {
  const loadRecordSource = async () => {
    if (!selectedRabota) return;

    try {

      const recordSource = await getFilteredDopRab(
        dopRab,
        parseInt(responsible),
        selectedDate,
        flagTipRab!,              
        selectedRabota?.sprRabota_Kompl_ID || 0, 
      );
    
    } catch (error) {
      console.error('Ошибка при загрузке recordSource:', error);
    } 
  };
  loadRecordSource();

}, [selectedRabota, flagTipRab, responsible, selectedDate])



useEffect(() => {
  setIsChangingResponsible(true);
  setSelectedDate('');
  setSelectedMaterials([]);
  
  // Сбрасываем флаг после небольшой задержки
  const timer = setTimeout(() => {
    setIsChangingResponsible(false);
  }, 100);
  
  return () => clearTimeout(timer);
}, [responsible]);

//////////////////////////////////////////////////////////////////////////
  const getCurrentUser = (): string => {
    return localStorage.getItem('userLogin')  || 'aalihacheva';
  };
 ////////////// Сохранение данных (аналог Кнопка12_Click)///////////////////////////
  const handleSave = async () => {

    
    const objectId = selectedRabota?.object_ID;
    const projectRabotaId = selectedRabota?.projectRabota_ID;
    console.log('Сохранение данных дополнительных материалов',{objectId,projectRabotaId,responsible,selectedDate});
    console.log('selectedMaterials при сохранении=', selectedMaterials);
    

    try {

  //     if (flagTipRab==='Мелкая') {
  // // Сначала сохраняем выбранные материалы во временную таблицу
  //     await saveToTmpMat(
  //       getCurrentUser(),
  //       selectedMaterials,
  //     );

  //     }

      if(poKrupnym===false){
        if (!objectId || !projectRabotaId || !responsible || !selectedDate) {
          alert("Не все обязательные поля заполнены!");
          return;
        }
          // 1. Проверяем и обновляем DopRab (аналог первой части Кнопка12_Click)
          // Здесь заносится информация об обосновании, доп.объеме, ответственном и дате.
          const dopRabUpdated = await updateDopRab({
            objectId,
            projectRabotaId,
            rabotaPrim: selectedRabota?.rabotaPrim || ' ',
            sprRzchId: parseInt(responsible),
            data: selectedDate,
            objemDop: objemDop || 0,
            obosn: obosn || '',
            zatr: 0
          });

          if (!dopRabUpdated) {
            alert("Отсутствуют данные в таблице дополнительных объемов работ <DopRab>!");
            return;
          }

          if (selectedMaterials.length>0){
            // 2. Сохраняем выбранные материалы в Material_dop (аналог второй части Кнопка12_Click)
            await saveMaterialDop({
              materials: selectedMaterials,
              objectId,
              projectRabotaId,
              rabotaPrim: selectedRabota?.rabotaPrim || ' ',
              sprRzchId: parseInt(responsible),
              data: selectedDate,
              rabotaKomplId: selectedRabota?.sprRabota_Kompl_ID || 0,
              user: getCurrentUser()
            });

            alert("Данные о работе успешно сохранены!");
            onClose();
          }

      
      }
      else {
        alert('Вы завели работу по крупным!');
      }    
    } catch (error) {
      console.error('Ошибка сохранения:', error);
      alert("Ошибка при сохранении данных!");
    }
  };

  const[newObjemDialogOpen,setNewObjemDialogOpen] = useState(false);
  
  


  const handleNewButtonClick = () => {
    setNewObjemDialogOpen(true);
  }

  const handleRubricatorSuccess = (sprRzch_ID: number, data?: Date) => {
      console.log('Успешно создан рубрикатор:', { sprRzch_ID, data });
      setResponsible(sprRzch_ID.toString());
      setNewObjemDialogOpen(false);
  }

  
  const handleClose = () => {
    onClose();
  };


useEffect(()=>{
  if(newObjemDialogOpen===false && propData){
      loadSprRzch(propData);
      loadDopRab(propData);
  }
},[newObjemDialogOpen, propData])


  return (
    
    <Dialog
      open={open}
      onClose={handleClose}
      maxWidth="lg"
      fullWidth
      PaperProps={{
        sx: {
          borderRadius: 2,
          minHeight: '80vh',
          width: '80%'
        }
      }}
    >
      <DialogTitle sx={{ 
        m: 0, 
        p: 2,
        backgroundColor: 'var(--color-accent-active)',
        color: 'white'
      }}>
        <Typography variant="h6" component="div">
          Дополнительные материалы
        </Typography>
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: 'white',
          }}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>

      <DialogContent dividers sx={{ p: 3 }}>
        {/* Информация об объекте и работе */}
        <Stack spacing={1} sx={{ mb: 1 }}>
          <Box>
            <Typography variant="subtitle1" color="text.secondary">
              Объект{selectedRabota?.object_ID ? ` №${selectedRabota.object_ID}` : ''}:
              <Typography component="span" fontWeight="600" sx={{ ml: 1 }}>
                {nameOfObject || 'Не выбран'}
              </Typography>
            </Typography>
          </Box>

          <Box>
            <Typography variant="subtitle1" color="text.secondary">
              Смета:
              <Typography component="span" fontWeight="600" sx={{ ml: 1 }}>
                {selectedRabota?.smeta || 'Не выбрана'}
              </Typography>
            </Typography>
          </Box>

          <Box>
            <Typography variant="subtitle1" color="text.secondary">
              Работа:
              <Typography component="span" fontWeight="600" sx={{ ml: 1 }}>
                {selectedRabota?.sprRabota || 'Не выбрана'}
                {selectedRabota?.sprRabota_Kompl && ` - ${selectedRabota.sprRabota_Kompl}`}
              </Typography>
            </Typography>
          </Box>
        </Stack>

        <Divider sx={{ my: 2 }} />

        {/* Объем работы и чекбокс */}
        <Grid container spacing={3} alignItems="center" sx={{ mb: 3 }}>
          <Grid size={{ xs: 12, sm: 6 }}>
            <Typography variant="subtitle2" gutterBottom>
              Объём работы дополнительно:
            </Typography>
            <TextField
              fullWidth
              disabled={poKrupnym}
              size="small"
              placeholder="Введите объем"
              value={objemDop}
              onChange={(e) => setObjemDop(Number(e.target.value))}
              sx={{ maxWidth: 200 }}
            />
          </Grid>
          
          <Grid size={{ xs: 12, sm: 6 }}>
            <FormControlLabel 
              control={<Checkbox color="primary" />} 
              label={
                <Typography variant="body2">
                  100% неэффективные затраты
                </Typography>
              } 
            />
            <FormControlLabel 
              control={
                <Checkbox 
                  color="primary" 
                  checked={poKrupnym}
                  onChange={(e) => setPoKrupnym(e.target.checked)}
                  disabled = {flagTipRab === 'Крупная'}
                />
              } 
              label={
                <Typography variant="body2">
                  По крупным
                </Typography>
              } 
            />
          </Grid>
        </Grid>

        {/* Обоснование */}
        <Box sx={{ mb: 1 }}>
          <Typography variant="subtitle2" gutterBottom>
            Обоснование:
          </Typography>
          <TextField
            id="obosnovanie"
            fullWidth
            multiline
            disabled={poKrupnym}
            value={obosn}
            onChange={(e) => setObosn(e.target.value)}
            rows={3}
            placeholder="Введите обоснование..."
            size="small"
          />
        </Box>

        <Divider sx={{ my: 2 }} />

        {/* Ответственный, дата и кнопка */}
        <Grid container spacing={2} alignItems="center" sx={{ mb: 3 }}>
          <Grid size={{ xs: 12, sm: 4 }}>
            <FormControl fullWidth size="small">
              <InputLabel id="responsib-label">Ответственный</InputLabel>
              <Select
                labelId="responsib-label"
                id="responsib"
                disabled={poKrupnym}
                value={responsible}
                label="Ответственный"
                onChange={(e) => setResponsible(e.target.value)}
              >
                <MenuItem value="">
                  <em>Не выбран</em>
                </MenuItem>
                {sprRzchList.map((item) => (
                  <MenuItem key={item.sprRzch_ID} value={item.sprRzch_ID}>
                    {item.nomer}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>


          <Grid size={{ xs: 12, sm: 4 }}>
            <FormControl fullWidth size="small">
              <InputLabel id="date-label">Дата</InputLabel>
              <Select
                labelId="date-label"
                id="date-select"
                value={selectedDate}
                label="Дата"
                disabled={responsible === "" || poKrupnym}
                onChange={(e) => setSelectedDate(e.target.value)}
              >
                <MenuItem value="">
                  <em>Не выбрана</em>
                </MenuItem>
                {availableDates.map((date, index) => (
                  <MenuItem key={index} value={date}>
                    {date}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>

          <Grid size={{ xs: 12, sm: 4 }}>
            <Button 
              fullWidth
              disabled={poKrupnym}
              variant="outlined"
              onClick = {handleNewButtonClick}
              sx={{
                py: 1,
                color: 'var(--color-accent-active)',
                borderColor: 'var(--color-accent-active)',
                border: 'solid',
                fontWeight: 600,
                
                borderWidth: 2,
                '&:hover': {
                  borderWidth: 2,
                  backgroundColor: 'primary.main',
                  color: 'white'
                },
              }}
            >
              Новый объем
            </Button>
          </Grid>
        </Grid>

        {/* Таблица материалов */}
        <Box sx={{ mt: 3 }}>
          <MaterialTable 
          onSelectedMaterialsSet={handleSelectedMaterialsSet} 
          filteredMaterials={selectedMaterials}
          osnovnye={false}
          />
        </Box>
      </DialogContent>

      <DialogActions sx={{ p: 2, gap: 1 }}>
        <Button 
          onClick={handleClose}
          variant="outlined"
          color="inherit"
        >
          Отмена
        </Button>
        <Button 
          onClick={handleSave}
          variant="contained"
          sx={{
            backgroundColor: 'var(--color-accent-active)',
            '&:hover': {
              backgroundColor: 'var(--color-accent-active-dark)',
            }
          }}
        >
          Сохранить
        </Button>
      </DialogActions>

      {newObjemDialogOpen && 

      <RubricatorDialog 
        open={newObjemDialogOpen} 
        onClose={() => setNewObjemDialogOpen(false)}
        onSuccess={handleRubricatorSuccess}
        objectId={selectedRabota?.object_ID || 0}
        projectRabotaId={selectedRabota?.projectRabota_ID || 0}
        rabotaPrim={selectedRabota?.rabotaPrim || " "}
        sprRabotaKomplId={selectedRabota?.sprRabota_Kompl_ID}
        flagTipRab={flagTipRab || "Крупная"}
      /> 
    }
    </Dialog>
  );
    
};

export default MaterialDopDialog;