// components/RubricatorDialog.tsx
import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Typography,
  Box,
  Alert
} from '@mui/material';
import { createDopRab, executeKompInsDopRab, getSprRzchByNomer } from '../../services/rubricatorService';

interface RubricatorDialogProps {
  open: boolean;
  onClose: () => void;
  onSuccess: (sprRzchId: number, data?: Date) => void;
  objectId: number;
  projectRabotaId: number;
  rabotaPrim: string;
  sprRabotaKomplId?: number;
  flagTipRab: string;
}

const RubricatorDialog: React.FC<RubricatorDialogProps> = ({
  open,
  onClose,
  onSuccess,
  objectId,
  projectRabotaId,
  rabotaPrim,
  sprRabotaKomplId = 0,
  flagTipRab
}) => {
  const [rubricator, setRubricator] = useState<string>('');
  const [error, setError] = useState<string>('');
  const [loading, setLoading] = useState(false);

  const validateRubricator = (value: string): boolean => {
    const num = parseInt(value);
    return [3, 4, 5, 6].includes(num);
  };

  const handleSubmit = async () => {
    const num = parseInt(rubricator);
    
    if (!validateRubricator(rubricator)) {
      setError('Введен неверный рубрикатор! Допустимые значения: 3, 4, 5, 6');
      return;
    }

    setLoading(true);
    setError('');

    try {
      // 1. Получаем SprRzch_ID по номеру рубрикатора
      const sprRzch = await getSprRzchByNomer(rubricator.trim());
      
      // 2. Создаем запись в DopRab
      const request = {
        objectId,
        projectRabotaId,
        rabotaPrim,
        rubricator: num,
        sprRabotaKomplId: flagTipRab === "Крупная" ? 0 : sprRabotaKomplId,
        flagTipRab,
        objemDop: 0
      };

      // 3. Выполняем хранимую процедуру 
      const result = await executeKompInsDopRab(request);

      if (result.success) {
        onSuccess(sprRzch.sprRzch_ID, result.data);
        handleClose();
      } else {
        setError(result.message || 'Ошибка при создании записи');
      }

    } catch (error) {
      console.error('Ошибка:', error);
      setError('Произошла ошибка при обработке рубрикатора');
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => {
    setRubricator('');
    setError('');
    onClose();
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setRubricator(e.target.value);
    if (error) setError('');
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSubmit();
    }
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      maxWidth="sm"
      fullWidth
      PaperProps={{
        sx: {
          borderRadius: 2,
          p: 1
        }
      }}
    >
      <DialogTitle>
        <Typography variant="h6" component="div" align="center">
          Ввод рубрикатора
        </Typography>
      </DialogTitle>

      <DialogContent>
        <Box sx={{ mb: 2 }}>
          <Typography variant="body1" align="center" sx={{ mb: 2 }}>
            Введите номер рубрикатора (3, 4, 5 или 6)
          </Typography>
          
          <TextField
            fullWidth
            label="Рубрикатор"
            value={rubricator}
            onChange={handleInputChange}
            onKeyPress={handleKeyPress}
            type="number"
            inputProps={{ 
              min: 3, 
              max: 6,
              style: { textAlign: 'center' }
            }}
            error={!!error}
            helperText={error ? error : "Допустимые значения: 3, 4, 5, 6"}
            autoFocus
            disabled={loading}
          />
        </Box>

        {error && (
          <Alert severity="error" sx={{ mt: 1 }}>
            {error}
          </Alert>
        )}
      </DialogContent>

      <DialogActions sx={{ justifyContent: 'center', gap: 2, p: 3 }}>
        <Button 
          onClick={handleClose}
          variant="outlined"
          color="inherit"
          disabled={loading}
        >
          Отмена
        </Button>
        <Button 
          onClick={handleSubmit}
          variant="contained"
          disabled={!rubricator.trim() || loading}
        >
          {loading ? 'Обработка...' : 'OK'}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default RubricatorDialog;