import * as React from 'react';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import { useEffect, useState } from 'react';
import  Rabota from '../../models/Rabota';
import { CircularProgress, Typography, Box, TextField, Button } from '@mui/material';
import { IRabotaTableProps } from '../../interfaces/IRabotaTableProps';
import { useNavigate, useParams } from 'react-router-dom';

import { doubleClick, etajNalichie, KudaZanositObjem, ObjemPrognoz_AfterUpdate, ObjSekciya_AfterUpdate } from '../../services/httpService';
import { getSprRabotaKomplIdFromMaterial } from '../../services/materialService';
import { handleFormClose, updateObjemObsh, updateProcent } from '../../services/formCloseService';
import { updateKomplMainView1 } from '../../services/komplService';
import { stableSort, getComparator } from '../../utils/sort-tables';


///// Создание колонок таблицы /////////////////////////////////////

interface Column {
  id: keyof Rabota;
  label: string;
  minWidth?: number;
  align?: 'right' | 'left' | 'center';
  format?: (value: any) => string;
  sortable?: boolean;
  clickable?: boolean;
}

type Order = 'asc' | 'desc';

// Основные колонки
const mainColumns: readonly Column[] = [
  { id: 'смета_ID', label: 'ID', minWidth: 10, sortable: true },
  { id: 'smeta', label: 'Смета', minWidth: 140, sortable: true },
  { id: 'sprRabota', label: 'Работа', minWidth: 120 },
  { id: 'rabotaPrim', label: 'Работа (прим)', minWidth: 30, align: 'center' },
  { 
    id: 'objemObsh', 
    label: 'Объем раб. общ.', 
    minWidth: 50, 
    align: 'center',
    format: (value: number) => value?.toLocaleString('ru-RU', {minimumFractionDigits: 2}) || "",
    sortable: true
  },
  { id: 'edIzm', label: 'Ед. изм.', minWidth: 40, align: 'center' },
 
  { 
    id: 'objemDop', 
    label: 'Доп. работа', 
    minWidth: 30, 
    align: 'center',
    format: (value: number) => value?.toLocaleString('ru-RU', {minimumFractionDigits: 2}) || "",
    clickable: true,
    sortable: true
  },
  { 
    id: 'objemPrognoz',
    label: 'Объем прогноз', 
    minWidth: 30, 
    align: 'center',
    format: (value: number) => value?.toLocaleString('ru-RU', {minimumFractionDigits: 2}) || ""
  },
   { id: 'procent', label: 'Процент', minWidth: 20, align: 'center' },


  { id: 'objem', 
    label: 'Объем раб.', 
    minWidth: 20, 
    align: 'center',
    format: (value: number) => value?.toLocaleString('ru-RU', {maximumFractionDigits: 5}) || "",
    clickable: true },
  //На верхнем уровне таблицы
  // { id: 'sekciya', label: 'Секция', minWidth: 70, align: 'center' },


  // TODO Пойдет на нижний уровень таблицы
  // { id: 'comment', label: 'Комментарий', minWidth: 40 },
 // { id: 'sprRabota_Rabota', label: 'Комплектная работа', minWidth: 100 },
];

// Функция для создания колонок этажей, чтобы не расписывать вручную
const createFloorColumns = (): Column[] => {
  const floorColumns: Column[] = [];
  
  // Специальные этажи
  floorColumns.push(
    { id: 'podval', label: 'Под.', minWidth: 10, align: 'center', format: (value: number) => value?.toLocaleString('ru-RU') || "" },
    { id: 'tehEtaj', label: 'Тех.э.', minWidth: 10, align: 'center', format: (value: number) => value?.toLocaleString('ru-RU') || "" },
    { id: 'cherdak', label: 'Чер.', minWidth: 10, align: 'center', format: (value: number) => value?.toLocaleString('ru-RU') || "" }
  );
  
  // Обычные этажи (1-40)
  for (let i = 1; i <= 40; i++) {
    const floorKey = `etaj${i}` as keyof Rabota;
    floorColumns.push({
      id: floorKey,
      label: `${i}`,
      minWidth: 2,
      align: 'center',
      format: (value: number) => value?.toLocaleString('ru-RU') || ""
    });
  }
  
  return floorColumns;
};
const floorColumns = createFloorColumns();
const allColumns = [...mainColumns, ...floorColumns];
///////////////////////////////////////////////////////////////////////


const RabotaTable: React.FC<IRabotaTableProps> = ({ 
  object_ID,
  data: propData, 
  currentPage, 
  onPageChange,
  onCurrentRowChange,
  dopRabotaPairs,
  onFlagTipRabChange,
  currentRow 
}) => {


  const [localData, setLocalData] = useState<Rabota[]>([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(100);

  // сортировка
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<keyof Rabota>('смета_ID');


  const [selectedRow, setSelectedRow] = useState<Rabota | null>(null); 
  const [isDirty, setIsDirty] = useState(false); // признак изменений
  const [editCell, setEditCell] = useState<{rowKey: string; columnId: string} | null>(null);


  const [activeControl, setActiveControl] = useState<string>('');
  const validValues = [514, 588, 609, 1374, 1376, 1664, 1665, 1666, 1667, 1668, 1669, 2230, 2231, 2233, 2236];

  const getRowKey = (row: Rabota) =>
  `${row.object_ID}-${row.projectRabota_ID}-${row.sekciya}`;

///////////////////////////////////////////////////монтирование//////////////////

// НОВЫЙ useEffect: устанавливает выделение извне (из parent)
    useEffect(() => {
        if (currentRow && !selectedRow) {  // Только если не выделено локально
            setSelectedRow(currentRow);
        }
    }, [currentRow]);

  useEffect(() => {
        if (selectedRow) {
            // ИСПРАВЛЕНИЕ: добавить sekciya в ID для уникальности!
            const element = document.getElementById(
                `${selectedRow.object_ID}-${selectedRow.projectRabota_ID}-${selectedRow.sekciya}`
            );
            if (element) {
                element.scrollIntoView({ behavior: 'smooth', block: 'center' });
            }
        }
    }, [selectedRow]);

  useEffect(() => {
    if(propData) {
      console.log('В компоненте rabota table получены новые данные', propData)
      setLocalData(propData);
    }
  },[propData])

   useEffect(() => {
    return () => {
      updateObjemObsh(object_ID).catch(error => {
        console.error('Ошибка при автоматическом закрытии формы:', error);
      });

      updateProcent(object_ID).catch(error => {
        console.error('Ошибка при автоматическом закрытии формы:', error);
      });
    };
  }, [page, object_ID]);



  /////////////////////////////////////////////////////////////////////////////

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  const handleRequestSort = (property: keyof Rabota) => {
    if (orderBy === property) {
    // Если кликаем на ту же колонку - меняем порядок
    if (order === 'asc') {
      setOrder('desc');
    } else {
      // Сбрасываем сортировку при третьем клике
      setOrder('asc');
      setOrderBy('smeta'); 
    }
  } else {

    setOrder('asc');
    setOrderBy(property);
  }
  };

  const handleDoubleClick = async (column: Column, row: Rabota, isText: boolean) => {
    if (!column.clickable && !isText) return;
    
    try {
      console.log('Выбран объект: ',row, column);

      var flagTipRab;
      var tipEtaj;
      var etajId;
      var objemEtaj;
      var isBreaks = false;
      isBreaks = await etajNalichie(row.projectRabota_ID);

      if (column.id.toString() === 'objem'){
        if (isBreaks === true){
          alert(`Работа '${row.sprRabota}' разбивается по этажам! Выберите требуемый этаж!"`);
          return;
        }
        console.log('isBreaks=', isBreaks);
      } 
      else {
        if (isBreaks === false && column.id.toString()!=='objemDop') {
        alert(`Работа '${row.sprRabota}' не разбивается по этажам! Внесите объем на весь этаж."`);
          return;
      }
      }

      

      if (isText && isBreaks === true){
        var res = await doubleClick(row, row.projectRabota_ID, row.sprRabota, row.sekciya, column.id.toString(), row[column.id]);
        if (res && res === true) {
            console.log('res=', res);
            alert('Заводим по мелким');
            flagTipRab = "Мелкая";
            tipEtaj = column.id.toString(); // потом поменять если что
            etajId = column.id.toString();
            objemEtaj = row[column.id];
            onPageChange(1, row);
        } else if (res!=null){
            let sprRabota_Kompl_ID = await getSprRabotaKomplIdFromMaterial(row);
            
            if ((sprRabota_Kompl_ID && sprRabota_Kompl_ID > 0) || sprRabota_Kompl_ID === null){
              console.log('sprRabota_Kompl_ID=',sprRabota_Kompl_ID);
              alert('Заводим по мелким');
              flagTipRab = "Мелкая";
              tipEtaj = column.id.toString(); // потом поменять если что
              etajId = column.id.toString();
              objemEtaj = row[column.id];
              onPageChange(1, row);
            } else if (sprRabota_Kompl_ID === 0){
              console.log('sprRabota_Kompl_ID=',sprRabota_Kompl_ID);
              alert('Заводим по крупным');
              flagTipRab = "Крупная";
              tipEtaj = column.id.toString(); // потом поменять если что
              etajId = column.id.toString();
              objemEtaj = row[column.id];
              const Rabota_Kompl_ID = 0;
              onPageChange(3, row);
            } else {alert('Вообще ничего не работает')}
        }
        
      }
      else{
        const response = await fetch("http://localhost:5189/api/rabota/check", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            objectId: row.object_ID,
            projectRabotaId: row.projectRabota_ID,
            rabotaPrim: row.rabotaPrim,
            sprRabota_Kompl_ID: row.sprRabota_Kompl_ID
          })
        });

        const result = await response.json();
        onFlagTipRabChange(result.result);
        // alert(result.message);

        if (result.result === "Мелкая") {
          onPageChange(1, row);  // KomplView2
        } else {
          onPageChange(3, row);  // Материал
        }
      }
      
    } catch (err) {
      console.error("Ошибка проверки работы", err);
      alert("Не удалось проверить работу");
    }
  };
  
  const handleCellClick = async (column: Column, row: Rabota) => {
    setSelectedRow(row);
    onCurrentRowChange(row);
  };


  /////////////////////////////////// Изменение в ячейках////////////////////////////
  const [tempValues, setTempValues] = useState<{[key: string]: string}>({});

  const handleCellChange = async (rabota:Rabota, rowKey: string, columnId: string, newValue: any, isText: boolean) => {
      // const cleanedValue = newValue.replace(/[^\d,.-]/g, '');

      // const valueForParsing = cleanedValue.replace(',', '.');
      // const numericValue = parseFloat(valueForParsing);
      // const isNumber = !isNaN(numericValue) && isFinite(numericValue);
      
      // const hasInvalidChars = newValue !== cleanedValue;
      let finalValue: string | number | null;
      const validPattern = /^-?\d*(?:[.,ю/]\d*)?$/;
      if (!validPattern.test(newValue)) {
        alert("Допускаются только цифры, запятая, точка и минус!");
        return;
      }
    
      if (!validValues.includes(rabota.projectRabota_ID) && isText){
        alert(`Работа '${rabota.sprRabota}' не разбивается по этажам! Выберите поле "Объем раб.!"`);
        return;
      }

      // Сохраняем как строку во временное состояние
      setTempValues(prev => ({
        ...prev,
        [`${rowKey}-${columnId}`]: newValue
      }));
      
      if (columnId === 'procent') {
        // Разрешаем только целые числа от 0 до 100
        const intValue = Math.floor(newValue);

        if (isNaN(intValue) || intValue < 0 || intValue > 100) {
          alert("Процент должен быть целым числом от 0 до 100");
          return;
        }

        finalValue = intValue;
      } else {
        finalValue = newValue  === "" ? null : newValue;
      }
      setLocalData((prev) =>
        prev.map((r) => (getRowKey(r) === rowKey ? { ...r, [columnId]: finalValue } : r))
      );

          // if (columnId === 'objem' || columnId === 'objemPrognoz' || columnId === 'procent' || columnId === 'objemDop') {

          //   const updatedRabota = updatedData.find(r => getRowKey(r) === rowKey);
          //   if (updatedRabota) {
          //     setTimeout(async () => {
          //       try {
                  

          //         if (isText) {
                    
          //           const result = await KudaZanositObjem(updatedRabota);
          //           if (result) {

          //             setLocalData(currentData => currentData.map(r => 
          //               r.projectRabota_ID === updatedRabota.projectRabota_ID 
          //               &&  r.object_ID === updatedRabota.object_ID
          //               &&  r.смета_ID === updatedRabota.смета_ID
          //               && r.sekciya === updatedRabota.sekciya
          //                 ? { ...r, objem: result.objSekciya, 
          //                   calculatedObjSekciya: result.objSekciya }
          //                 : r
          //             ));
          //           }

                    
          //         } 
          //         // else if (columnId === 'objem') {
          //         //   await ObjSekciya_AfterUpdate(updatedRabota);
          //         // } else if (columnId === 'objemPrognoz') {
          //         //   await ObjemPrognoz_AfterUpdate(updatedRabota, numericValue);
          //         // }

          //         const saveSuccess = await updateKomplMainView1(updatedRabota);
          //           if (saveSuccess) {
          //             console.log('Данные успешно сохранены в KomplMainView1');
          //           }
          //       } catch (error) {
          //         console.error(`Ошибка при обработке поля ${columnId}:`, error);
          //       }
          //     }, 0);
          //   }
          // }
          
          // return updatedData;
      // });
      setIsDirty(true);

  };

// При потере фокуса конвертируем в число
const handleCellBlur = (rowKey: string, columnId: string) => {
  const tempKey = `${rowKey}-${columnId}`;
  const tempValue = tempValues[tempKey];

  if (!tempValue || tempValue === "") {
      setEditCell(null);
      
      setTempValues(prev => {
        const newTemp = {...prev};
        delete newTemp[tempKey];
        return newTemp;
      });
      return;
    }
  
    const cleaned = tempValue.replace(',', '.');
  const numericValue = parseFloat(cleaned);

  const updatedRabota = localData.find(r => getRowKey(r) === rowKey);
    if (updatedRabota) {
      const key = columnId as keyof Rabota;
      (updatedRabota[key] as any) = isNaN(numericValue) ? null : numericValue;

      setTimeout(async () => {
        try {
            
            const result = await KudaZanositObjem(updatedRabota);
            if (result) {

              setLocalData(currentData => currentData.map(r => 
                r.projectRabota_ID === updatedRabota.projectRabota_ID 
                &&  r.object_ID === updatedRabota.object_ID
                &&  r.смета_ID === updatedRabota.смета_ID
                && r.sekciya === updatedRabota.sekciya
                  ? { ...r, objem: result.objSekciya, 
                    calculatedObjSekciya: result.objSekciya }
                  : r
              ));
            }

          const saveSuccess = await updateKomplMainView1(updatedRabota);
            if (saveSuccess) {
              console.log('Данные успешно сохранены в KomplMainView1');
            }
        } catch (error) {
          console.error(`Ошибка при обработке поля ${columnId}:`, error);
        }
      }, 0);
    }
  
          
  if (tempValue && (tempValue.endsWith(',') || tempValue.endsWith('.'))) {

    const numericValue = parseFloat(tempValue.slice(0, -1).replace(',', '.'));
    
    if (!isNaN(numericValue)) {
      setLocalData(prev => {
        return prev.map(r => {
          const key = getRowKey(r);
          if (key === rowKey) {
            return { ...r, [columnId]: numericValue };
          }
          return r;
        });
      });
    }
  }
  
  // Очищаем временное значение
  setTempValues(prev => {
    const newTemp = {...prev};
    delete newTemp[tempKey];
    return newTemp;
  });
};



  return (
    <Box sx={{pl:2, pr:2}}>
      <TableContainer sx={{ flex: 1, maxHeight: 'calc(100vh - 200px)' }}>
        <Table stickyHeader aria-label="Rabota table" size="small">
          <TableHead>
            <TableRow>
              {allColumns.map((column) => (
                <TableCell
                  key={column.id.toString()}
                  align={column.align}
                  style={{ 
                    fontSize: '0.75em',
                    lineHeight: '1.3em',
                    minWidth: column.minWidth,
                    fontWeight: 'bold',
                    backgroundColor: '#f5f5f5',
                    cursor: column.sortable ? 'pointer' : 'default'
                  }}
                  onClick={() => {
                    if (column.sortable) {
                      handleRequestSort(column.id);
                    }
                  }}

                >
                  {column.label}
                  {orderBy === column.id ? (order === 'asc' ? ' ⇡' : ' ⇣') : column.sortable ? ' ↕' : ''}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>


          <TableBody>
            {stableSort(localData, getComparator(order, orderBy))
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row, index) => {

                const isSelected = selectedRow?.komplMainView1_ID === row.komplMainView1_ID && selectedRow?.object_ID === row.object_ID && 
                                 selectedRow?.projectRabota_ID === row.projectRabota_ID;

                const rowKey = getRowKey(row);

                // console.log('Пары с доп работой', dopRabotaPairs);
                // Проверяем, есть ли текущая строка в списке доп работ
                const hasDopRabota = dopRabotaPairs.some(pair => 
                  pair.смета_ID === row.смета_ID && 
                  pair.projectRabota_ID === row.projectRabota_ID
                );

                // if (hasDopRabota) {
                //   console.log('Строка с доп работами:', {
                //     смета_ID: row.смета_ID,
                //     projectRabota_ID: row.projectRabota_ID,
                //     smeta: row.smeta,
                //     sprRabota: row.sprRabota
                //   });
                // }


                return (
                  <TableRow 
                    hover 
                    role="checkbox" 
                    tabIndex={-1} 
                    id={`${row.object_ID}-${row.projectRabota_ID}-${row.sekciya}`}
                    key={`${row.object_ID}-${row.projectRabota_ID}-${row.sekciya}-${index}`}
                      sx={{ 
                      backgroundColor: isSelected ? '#cce5f7ff' : 'inherit',
                      '&:hover': {
                        backgroundColor: isSelected ? '#bbdefb' : '#f5f5f5'
                      }
                    }}
                  >
                    {allColumns.map((column) => {
                      const value = row[column.id];
                      

                      const isEditable = (allColumns.findIndex(c => c.id === column.id) >= 
                                         allColumns.findIndex(c => c.id === "objemDop")); // редактируем с objemDop и дальше

                      const isEditing = editCell?.rowKey === rowKey && editCell?.columnId === column.id;
                      const isText = allColumns.findIndex(c => c.id === column.id) >= 
                                         allColumns.findIndex(c => c.id === "podval"); 

                      // console.log(`Колонка: ${column.id}, Значение:`, value); 

                      // Определяем цвет для кликабельных ячеек в зависимости от наличия доп работ
                      let cellColor = 'inherit';
                      if (column.clickable && column.id !== 'objem') {
                        cellColor = hasDopRabota ? '#a8b9ebf6' : '#a8b9ebd3'; 
                      }

                      if (column.id === 'objemPrognoz'){
                        cellColor = '#b87fbd9a' ;
                      }

                      return (
                        <TableCell 
                          key={column.id.toString()} 
                          align={column.align}
                          sx={{ 
                            fontSize: '0.7rem',
                            py: 0.5,
                            color: column.clickable ? 'black' : 'inherit',
                            cursor: isEditable ? (isText ? 'text' : (column.clickable ? 'pointer' : "default")) : "default" ,
                            backgroundColor: cellColor,
                            borderLeft: hasDopRabota && column.clickable && column.id !== 'objem'  ? '2px solid #3672f4ff' : 'none'
                          }}
                          onFocus={() => setActiveControl(column.id.toString())}
                          onDoubleClick={()=>{handleDoubleClick(column,row,isText);}}
                           onClick={() => {
                            handleCellClick(column, row);
                            if (isEditable) {
                              setEditCell({rowKey, columnId: column.id.toString()});
                            }}
                          }
                        >
                        
                          {isEditing ? (
                            <TextField
                              value={value || ""}
                              onChange={(e) =>
                                handleCellChange(row, rowKey, column.id.toString(), e.target.value, isText)
                              }
                              onBlur={() => {
                                setEditCell(null);
                                handleCellBlur(rowKey, column.id.toString());
                              }}
                              autoFocus
                              size="small"
                              variant="standard"
                              InputProps={{
                                disableUnderline: true,
                                sx: {
                                  width:"30px",
                                  fontSize: "0.7rem",
                                  padding: 0,
                                  margin: 0,
                                  "& input": {
                                    padding: 0,
                                    margin: 0,
                                    textAlign: column.align || "left", // сохраняем выравнивание
                                  },
                                },
                              }}
                              sx={{ width: "100%", background: "transparent" }}
                            />
                          ) : column.format && typeof value === "number" ? (
                            column.format(value)
                          ) : (
                            value || "-"
                          )}

                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      
      {isDirty && (
        <Box sx={{mt:1}}>
        <Button 
          variant="contained" 
          onClick={() => {
            console.log("Сохраняем данные:", localData);
            setIsDirty(false);
          }}
        >
          Сохранить
        </Button>
      </Box>
      )}
      
      
      <TablePagination
        rowsPerPageOptions={[10, 25, 50, 100]}
        component="div"
        count={localData.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
        labelRowsPerPage="Строк на странице:"
        labelDisplayedRows={({ from, to, count }) => 
          `${from}-${to} из ${count !== -1 ? count : `более ${to}`}`
        }
      />
    </Box>
  );
}

export default RabotaTable;