import { useEffect, useState } from "react";

import {IMaterialsDialogProps} from "../../interfaces/IMaterialsDialogProps";
import { executeMaterialListProcedure, getMaterials } from "../../services/materialService";
import { Box, Button, CircularProgress, Dialog, DialogContent, DialogTitle, TextField, Typography } from "@mui/material";


import * as React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { height } from "@mui/system";
import { useCurrentUser } from "../../hooks/useCurrentUser";
import MaterialSummary from "../../models/MaterialSummary";


const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.white,
    fontWeight: 'bold',
    height: '1em'
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));


const MaterialsDialog: React.FC<IMaterialsDialogProps> = ({ objectId, open, nameOfObject, onClose }) => {
    const [materials, setMaterials] = useState<MaterialSummary[] | null>(null);
    const [loading, setLoading] = useState(false);
    const [editingRows, setEditingRows] = useState<Set<number>>(new Set());

    const { currentUser, loading: userLoading } = useCurrentUser();
    
    useEffect(() => {
        setLoading(true);
        const loadMaterials = async () => {
            if (open && objectId && currentUser && !userLoading) {
                
                try {
                    await executeMaterialListProcedure(objectId, currentUser);
                    
                    getMaterials(objectId)
                      .then(setMaterials)
                      .catch(err => console.error(err))
                      .finally(() => setLoading(false));
                    
                } catch (err) {
                    console.error('Ошибка загрузки материалов:', err);
                    setLoading(false);
                } 
            }
        };

        loadMaterials();
    }, [open, objectId, currentUser, userLoading]);

    // const toggleRowEdit = (index: number) => {
    //   setEditingRows(prev => {
    //     const newSet = new Set(prev);
    //     if (newSet.has(index)) {
    //       newSet.delete(index);
    //     } else {
    //       newSet.add(index);
    //     }
    //     return newSet;
    //   });
    // };

  // const handleEditClick = () => {
  //   if (editable) {
  //     // TODO СДЕЛАТЬ API ДЛЯ СОХРАНЕНИЯ В БД
  //     console.log("Сохраняем материалы:", materials);
  //   }
  //   setEditable(prev => !prev);
  // };

  // const handleChange = (index: number, field: keyof Material, value: string) => {
  //   if (!materials) return;
  //   const updated = [...materials];
  //   updated[index] = { ...updated[index], [field]: value };
  //   setMaterials(updated);
  // };

  // const handleSave = () => {
  //   console.log("Сохраняем все изменения:", materials);
  //   // TODO: вызвать API updateMaterials(objectId, materials)
  //   setEditingRows(new Set()); // сбросить редактирование
  // };

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth="md">
      <DialogTitle>
          <Box sx={{
            display: 'flex',
            justifyContent: 'space-between'
          }}>
            <Typography variant="h6" component="div">
            Список <i>основных материалов</i> объекта <strong> {nameOfObject} </strong>
            </Typography>

            {materials && (
              <Button 
              variant="contained" 
              // onClick={handleSave} 
              disabled={editingRows.size === 0}>
                Сохранить
              </Button>
            )}
          </Box>
        </DialogTitle>
      <DialogContent>
        {loading && (
          <div style={{ display: 'flex', justifyContent: 'center', padding: '20px' }}>
            <CircularProgress />
          </div>
        )}

        {!loading && materials && materials.length > 0 && (
          <TableContainer component={Paper} sx={{ marginTop: 1 }}>
            <Table sx={{ minWidth: 650 }} aria-label="материалы таблица">
              <TableHead>
                <TableRow sx={{ 
                    height: '30px',
                    '& .MuiTableCell-root': {
                      padding: '4px 8px',
                      lineHeight: '1.2'
                    }
                  }}>
                  <StyledTableCell>Материал</StyledTableCell>
                  <StyledTableCell align="right">Комментарий</StyledTableCell>
                  <StyledTableCell align="right">Объём</StyledTableCell>
                  <StyledTableCell align="right">Ед. измерения</StyledTableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {materials.map((material, index) => {
                  const isEditing = editingRows.has(index);
                  return (
                    <StyledTableRow 
                      key={index} 
                      // onDoubleClick={() => toggleRowEdit(index)} 
                      hover 
                      sx={{ cursor: "pointer" }}
                    >
                      <StyledTableCell>
                        {isEditing ? (
                          <TextField
                            value={material.sprMaterial || ""}
                            // onChange={e => handleChange(index, "sprMaterial", e.target.value)}
                            size="small"
                          />
                        ) : (
                          <Typography variant="body2" fontWeight="medium">
                            {material.sprMaterial || "Не указано"}
                          </Typography>
                        )}
                      </StyledTableCell>

                      <StyledTableCell align="right">
                        {isEditing ? (
                          <TextField
                            value={material.comment || ""}
                            // onChange={e => handleChange(index, "comment", e.target.value)}
                            size="small"
                          />
                        ) : (
                          <Typography variant="body2">{material.comment || "—"}</Typography>
                        )}
                      </StyledTableCell>

                      <StyledTableCell align="right">
                        {isEditing ? (
                          <TextField
                            type="number"
                            value={material.sumObjem || 0}
                            // onChange={e => handleChange(index, "sumObjem", e.target.value)}
                            size="small"
                          />
                        ) : (
                          <Typography variant="body2" fontWeight="bold">
                            {material.sumObjem?.toLocaleString('ru-RU', {
                              minimumFractionDigits: 2,
                              maximumFractionDigits: 2
                              }) || "0,00"}
                          </Typography>
                        )}
                      </StyledTableCell>

                      <StyledTableCell align="right">
                        {isEditing ? (
                          <TextField
                            value={material.edIzm || ""}
                            // onChange={e => handleChange(index, "edIzm", e.target.value)}
                            size="small"
                          />
                        ) : (
                          <Typography variant="body2">{material.edIzm || "—"}</Typography>
                        )}
                      </StyledTableCell>
                    </StyledTableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        )}

        {!loading && materials && materials.length === 0 && (
          <Typography variant="body2" sx={{ padding: 2, textAlign: 'center' }}>
            Нет данных об основных материалах
          </Typography>
        )}

        {!loading && !materials && (
          <Typography variant="body2" sx={{ padding: 2, textAlign: 'center' }}>
            Не удалось загрузить данные
          </Typography>
        )}
      </DialogContent>
    </Dialog>
  );
}

export default MaterialsDialog;