import { Box, Button, Grid, TextField, InputAdornment, Container } from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import './auth-page.module.css'
import PersonIcon from "@mui/icons-material/Person";
import LockIcon from "@mui/icons-material/Lock";

export default function AuthPage({ setIsAuthenticated }: { 
  setIsAuthenticated: (auth: boolean) => void 
}) {
    const textFieldStyles = {
        "& .MuiOutlinedInput-root": {
            color: "var(--color-accent)",
            "& fieldset": {
                borderColor: "#000000ff",
            },
            "&:hover fieldset": {
                borderColor: "var(--color-accent-hover)",
            },
            "&.Mui-focused fieldset": {
                borderColor: "var(--color-accent-active)",
            },
        },
    };

    const buttonStyles = {
        backgroundColor: "var(--color-accent-hover)",
        color: "white",
        height: "3em",
        borderRadius: "4px",
        textTransform: "none",
        transition: "all 0.3s ease",
        "&:hover": {
            backgroundColor: "var(--color-accent-active)",
        },
        "&:disabled": {
            backgroundColor: "rgba(0,0,0,0.12)",
            color: "rgba(0,0,0,0.26)",
        },
    };

    const [login, setLogin] = useState('')
    const [password, setPassword] = useState('l1n1')
    const [loginError, setLoginError] = useState('')
    const [isChecking, setIsChecking] = useState(false)
    const navigate = useNavigate()

    // Функция проверки существования пользователя
    const checkUserExists = async (userLogin: string) => {
        setIsChecking(true);
        setLoginError('');
        
        try {
            const response = await fetch('http://localhost:5189/api/auth/check-user', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    login: userLogin.toLowerCase().trim()
                })
            });

            if (response.ok) {
                const result = await response.json();
                return result.exists;
            } else {
                throw new Error('Ошибка проверки пользователя');
            }
        } catch (error) {
            console.error('Ошибка при проверке пользователя:', error);
            setLoginError('Ошибка соединения с сервером');
            return false;
        } finally {
            setIsChecking(false);
        }
    };

     // Функция для выхода
    const handleLogout = () => {
        localStorage.removeItem('userLogin');
        localStorage.removeItem('userName');
        localStorage.removeItem('userId');
        localStorage.removeItem('authToken');
        setIsAuthenticated(false);
    };

    const handleAuth = async (e: React.FormEvent) => {
        e.preventDefault();

        if (!login.trim() || !password.trim()) {
            setLoginError('Заполните все поля');
            return;
        }

        const userExists = await checkUserExists(login);
        if (!userExists) {
            setLoginError('Пользователь не найден в системе');
            return;
        }

        // Если пользователь существует, выполняем вход
        try {
            const response = await fetch('http://localhost:5189/api/auth/login', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    login: login.toLowerCase().trim(),
                    password: password
                })
            });

            if (response.ok) {
                navigate('/');
                localStorage.setItem('userLogin', login.toLowerCase().trim());
                setIsAuthenticated(true); 
                const userData = await response.json();
                if (userData && userData.user) {
                    localStorage.setItem('userData', JSON.stringify(userData.user));
                }
            } else {
                setLoginError('Ошибка авторизации');
            }
        } catch (error) {
            setLoginError('Ошибка соединения с сервером');
        }
    };

    return (
        <div className="auth-container">
            <Container maxWidth="sm" sx={{ display: 'flex', justifyContent: 'center', minHeight:'100vh' }} >
                <Box
                    component="form"
                    onSubmit={handleAuth}
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        gap: '2em',
                        padding: '3em',
                        width: '100%',
                        maxWidth: '500px',
                        backgroundColor: 'white',
                        borderRadius: '8px',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
                    }}>
                    
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%' }}>
                        <h1>Вход в систему</h1>
                        <Button 
                            onClick={handleLogout}
                            sx={{
                                
                                backgroundColor: 'transparent',
                                color: 'black',
                                border: '1px solid black',
                                borderRadius: '6px',
                                padding: '0.4em 1em',
                                textTransform: 'none',
                                fontWeight: 500,
                                fontSize: '0.8em',
                                '&:hover': {
                                    backgroundColor: 'var(--color-accent-hover)',
                                    color: 'white',
                                    borderColor: 'var(--color-accent-hover)',
                                },
                            }}
                        >
                            Выйти
                        </Button>
                    </Box>

                    <Grid container spacing={3} sx={{ width: '100%' }}>
                        {/* Логин */}
                        <Grid size={{ xs: 12 }}>
                            <TextField
                                label="Введите логин"
                                variant="outlined"
                                value={login}
                                onChange={(e) => setLogin(e.target.value)}
                                error={!!loginError}
                                helperText={loginError}
                                disabled={isChecking}
                                fullWidth
                                InputProps={{
                                    startAdornment: <InputAdornment position="start"><PersonIcon style={{ color: "#000000ff" }} /></InputAdornment>
                                }}
                                InputLabelProps={{ style: { color: "#1f1f1fff" } }}
                                sx={textFieldStyles}
                            />
                        </Grid>

                        {/* Пароль */}
                        <Grid size={{ xs: 12 }}>
                            <TextField
                                label="Пароль"
                                variant="outlined"
                                type="password"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                error={password.trim() === ""}
                                fullWidth
                                InputProps={{
                                    startAdornment: <InputAdornment position="start"><LockIcon style={{ color: "#000000ff" }} /></InputAdornment>
                                }}
                                InputLabelProps={{ style: { color: "#1f1f1fff" } }}
                                sx={textFieldStyles}
                            />
                        </Grid>

                        {/* Кнопка входа */}
                        <Grid size={{ xs: 12 }}>
                            <Button
                                type="submit"
                                variant="contained"
                                fullWidth
                                sx={buttonStyles}
                                size="large"
                                disabled={isChecking}
                            >
                                {isChecking ? 'Проверка...' : 'Войти'}
                            </Button>
                        </Grid>
                    </Grid>
                </Box>
            </Container>
        </div>
    );
}