import { IRabotaResponse } from "../interfaces/IRabotaResponse";
import DopRabota from "../models/DopRabota";


const API_URL = "http://localhost:5189/api/rabota/";

export async function getDopRabotaByObjectId(objectId: number): Promise<DopRabota[]> {
    const response = await fetch(`${API_URL}object/${objectId}/dopraboty`);

     if (!response.ok){
        throw new Error("Не удалось получить сведения");
    }

    const result = await response.json();
    // console.log('Ответ сервера о доп данных:', result.data );

    return result.data || [];
}


export async function getDopRabotaBySmeta(smetaId: number, projectRabId: number, data: DopRabota[] ): Promise<DopRabota[]> {
    if (!data || !Array.isArray(data)) {
        return [];
    }
    
    return data.filter(item => 
        item && 
        item.смета_ID == smetaId && 
        item.projectRabota_ID == projectRabId
    );
}

