import API_BASE_URL from "../config";
import SprRzch from "../models/SprRzch";

const API_URL = API_BASE_URL+"/api/spravochnik";

export async function getSprRzch(
objectId: number,
projectRabotaId: number,
rabotaPrim: string
): Promise<SprRzch[]>  {
    const requestBody = { objectId, projectRabotaId, rabotaPrim };
    const response = await fetch(`${API_URL}/getSprRzch`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(requestBody),
    });

    if (!response.ok){
        throw new Error("Не удалось получить сведения");
    }
    const result = await response.json();
    console.log(result);
    return result;
}

