
import { Material } from "../models/Material";
import SprMaterial from "../models/SprMaterial";


const API_URL = "http://localhost:5189/api/materialDop"
export async function change_data(request: {
    Object_ID: number;
    ProjectRabota_ID: number;
    RabotaPrim: string;
    SprRzch_ID: number;
    data: string;
    flagTipRab: string;
    SprRabota_Kompl_ID?: number;
}): Promise<Material[]> {

    const response = await fetch(`${API_URL}/GetDopMaterial`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(request),
    });

    if (!response.ok) {
        return [];
    }
    return await response.json();
}

