import { useLocation, useNavigate, useParams } from "react-router-dom";
import RabotaTable from "../../components/tables/rabota-table";
import { Box } from "@mui/system";
import { Button, CircularProgress, FormControl, InputLabel, MenuItem, NativeSelect, Paper, Select, SelectChangeEvent, Typography } from "@mui/material";
import { useEffect, useMemo, useState } from "react";
import { deleteRabota, getRabotaByObjectId, isExistDopRabota } from "../../services/rabotaService";
import Rabota from "../../models/Rabota";
import StatusMessage from "../../components/StatusMessage";
import { getDopRabotaByObjectId } from "../../services/dopRabotaService";
import DopRabota from "../../models/DopRabota";
import MaterialDop from "../../components/forms/material-dop";
import CustomButton from "../../components/buttons/CustomButton";
import { getCustomButtonStyles } from "../../utils/buttonsHelper";
import AddNewSectionDialog from "../../dialogs/rabota/addNewSection";
import DeleteRabotaDialog from "../../dialogs/rabota/deleteRabota";
import DeleteDialog from "../../dialogs/delete-dialog";
import { deleteMelkRabota } from "../../services/sprRabotaService";
import DopRabotaTable from "../../components/tables/dopRabota-table";


function RabotaPage() {
    const location = useLocation();
    const navigate = useNavigate();

    const { objectName } = location.state || {};
    const { objectId } = useParams<{ objectId: string }>();

    const objectIdNum = objectId ? parseInt(objectId, 10) : 0;
    const displayName = objectName || `${objectIdNum}`;

    const [data, setData] = useState<Rabota[]>([]);
    const [dopData, setDopData] = useState<DopRabota[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    
    const [page, setPage] = useState(0);
    
    
    const [selectedRow, setSelectedRow] = useState(null);
    const [selectedRabota, setSelectedRabota] = useState<Rabota | null>(null);

    const [pageSite, setPageSite] = useState(0);
    const handlePageChange = (newPageSite: number, selectedRow?: Rabota) => {
        setPageSite(newPageSite);
        if (selectedRow) {
            setSelectedRabota(selectedRow);
        }
    }

    const [currentRow, setCurrentRow] = useState<Rabota>();
    const handleCurrentRowChange = (newCurrentRow: Rabota) => {
        setCurrentRow(newCurrentRow);
           console.log('Выбранная строка:', { 
            newCurrentRow
        });
    }

    const [currentDopRabota, setCurrentDopRabota] = useState<DopRabota>();
    const handleCurrentDopRabota= (newRow: DopRabota) => {
        setCurrentDopRabota(newRow);
           console.log('Выбранная строка с доп работой:',
            newRow);
    }

    const [dopRabotaPairs, setDopRabotaPairs] = useState<Array<{смета_ID: number, projectRabota_ID: number}>>([]);
    
    const [flagTipRab, setFlagTipRab] = useState<string> ();
    const onFlagTipRabChange = (newFlagTipRab:string) => {
        setFlagTipRab(newFlagTipRab);
    }
   
    const [filteredData, setFilteredData] = useState<Rabota[]>([]);
////// Попытка сделать обновление данных без перезагрузки страниц////////////////
const refreshDopData = async () => {
    if (objectIdNum) {
        try {
            const DopRabotaData = await getDopRabotaByObjectId(objectIdNum);
            console.log('Обновленные данные о доп работе:', DopRabotaData);
            const safeDopData = Array.isArray(DopRabotaData) ? DopRabotaData : [];
            setDopData(safeDopData);
            
            // Если есть новая добавленная работа, выделяем её
            const shouldSelect = sessionStorage.getItem('shouldSelectNewRabota');
            const storedRabota = sessionStorage.getItem('newlyAddedRabota');
            
            if (shouldSelect && storedRabota) {
                const newRabota = JSON.parse(storedRabota);
                
                // Находим добавленную работу в обновленных данных
                const addedDopRabota = safeDopData.find(item => 
                    item.projectRabota_ID === newRabota.projectRabota_ID && 
                    item.sprRabota_Kompl_ID === newRabota.sprRabota_Rabota_ID
                );
                
                if (addedDopRabota) {
                    setCurrentDopRabota(addedDopRabota);
                    console.log('Выделена новая доп работа:', addedDopRabota);
                }
                
                sessionStorage.removeItem('shouldSelectNewRabota');
                sessionStorage.removeItem('newlyAddedRabota');
            }
        } catch (err) {
            console.error('Ошибка обновления данных:', err);
        }
    }
};

////// Удаление/ Добавление работ////////////////////////////////////////////////
    const [isChoiceFormOpen, setIsChoiceFormOpen] = useState(false);
        const handleOpenChoiceForm = () => {
        setIsChoiceFormOpen(true);
        };

        const handleCloseChoiceForm = () => {
            setIsChoiceFormOpen(false);
            if (refreshDopData) {
                refreshDopData(); 
            }
        };    
    const handleJobDeleted = () => {
        window.location.reload(); 
    };

    const handleSectionAdded = (newSelectedSection: number) => {
        localStorage.setItem('selectedSection', JSON.stringify(newSelectedSection));
        setSelectedSection(newSelectedSection);
        window.location.reload(); 
    }

    const handleAddButtonClick = () => {
        navigate(`/rabota/${objectId}/vyborMelk`, { 
            state: { 
            background: location,
            objectId: objectId,
            sekcii: sectionsArray ? sectionsArray : [],
            selectedSection: Number(selectedSection) ? selectedSection : 1,
            selectedRabota: selectedRabota
            } 
        });
    };


    const handleRabotaAdded = (newRabota: Rabota, pageSiteToReturn: number) => {
    setCurrentRow(newRabota);
    setSelectedRabota(newRabota);
    
    setPageSite(pageSiteToReturn);
    };

    
    const [isDialogAddSectionOpen, setIsDialogAddSectionOpen] = useState(false);
    const handleOpenDialogAddSection = () => {
        setIsDialogAddSectionOpen(true);
    }
    const handleClosingDialogAddSection = () => {
        setIsDialogAddSectionOpen(false);
    }


    const [isDialogDeleteRabotaOpen, setIsDialogDeleteRabotaOpen] = useState(false);
    const handleOpenDialogDeleteRabota= () => {
        console.log('currentRow = ', currentRow);
        setIsDialogDeleteRabotaOpen(true);
    }
    const handleClosingDialogDeleteRabota = () => {
        setIsDialogDeleteRabotaOpen(false);
    }


    const [isDialogDeleteRabotaMelkOpen, setIsDialogDeleteRabotaMelkOpen] = useState(false);
    const handleOpenDialogDeleteRabotaMelk= () => {
        console.log('currentRow = ', currentRow);
        setIsDialogDeleteRabotaMelkOpen(true);
    }
    const handleClosingDialogDeleteRabotaMelk = () => {
        setIsDialogDeleteRabotaMelkOpen(false);
    }


/////Загрузка данных/////////
useEffect(() => {
if (objectIdNum) {
    setLoading(true);
    setError(null);
    
    getRabotaByObjectId(objectIdNum)
    .then((RabotaData) => {
        console.log('Полученные данные:', RabotaData);
        console.log('Тип данных:', typeof RabotaData);
        console.log('Является ли массивом:', Array.isArray(RabotaData));
        console.log('Длина данных:', RabotaData?.length);
        
        const safeData = Array.isArray(RabotaData) ? RabotaData : [];
        setData(safeData);
        console.log('Установлены данные:', safeData);
    })
    .catch((err) => {
        console.error('Ошибка загрузки данных:', err);
        setError('Не удалось загрузить данные');
        setData([]);
    })
    .finally(() => {
        setLoading(false);
    });


    ///TODO продумать получение дополнительных данных
    getDopRabotaByObjectId(objectIdNum)
    .then((DopRabotaData) => {
        console.log('Полученные данные о доп работе:', DopRabotaData);
        const safeDopData = Array.isArray(DopRabotaData) ? DopRabotaData : [];
        setDopData(safeDopData);
    })
    .catch((err) => {
        console.error('Ошибка загрузки данных:', err);
    })


    isExistDopRabota(objectIdNum)
        .then(pairs => {
            console.log('isExistDopRabota запущена')
            setDopRabotaPairs(pairs);

            console.log('pairs = ',pairs);
        })
        .catch(error => {
            console.error('Ошибка при получении доп работ:', error);
        });
}
}, [objectIdNum]);

useEffect(() => {
    const restoreData = async () => {
        const shouldSelect = sessionStorage.getItem('shouldSelectNewRabota');
        const storedRabota = sessionStorage.getItem('newlyAddedRabota');
        
        await refreshDopData();

        try {

            const newData = await getRabotaByObjectId(objectIdNum);
            setData(newData);

            sessionStorage.removeItem('shouldSelectNewRabota');
            sessionStorage.removeItem('newlyAddedRabota');
            
            console.log('Восстановлены новые работы:', newData);
            if (selectedSection === 'all') {
                setFilteredData(newData);
            }
            setFilteredData(newData.filter(item => item.sekciya === selectedSection));
        } catch (error) {
            console.error('Ошибка при восстановлении данных:', error);
            sessionStorage.removeItem('shouldSelectNewRabota');
            sessionStorage.removeItem('newlyAddedRabota');
        }
        
    };

    restoreData();
}, [pageSite]);


useEffect(() => {
    (window as any).refreshDopData = refreshDopData;
    
    return () => {
        // Очищаем при размонтировании
        (window as any).refreshDopData = null;
    };
}, [objectIdNum]);



////////////////////////////////////////////////////////////////
///Работа с секциями
    const sections = useMemo(() => {
        const uniqueSections = 
                    Array.from(new Set(data.map(item => item.sekciya)))
                    .sort((a, b) => a - b);
        
        return uniqueSections;
    }, [data]);
    const sectionsArray: number[] = sections;
     const [selectedSection, setSelectedSection] = useState<number | 'all'>(() => {
        const saved = localStorage.getItem('selectedSection');
        const section = saved ? JSON.parse(saved) : 1;
        return sections.includes(section) ? section : 1;
    });

    // Фильтруем данные по выбранной секции
    useEffect(() => {
    if (selectedSection === 'all') {
        setFilteredData(data);
    } else {
        setFilteredData(data.filter(item => item.sekciya === selectedSection));
    }
}, [data, selectedSection]);

     // Фильтруем допданные по выбранной секции
    const filteredDopRabota = useMemo(() => {
    if (selectedSection === 'all') {
        return dopData;
    }
    return dopData.filter(item => item.sekciya === selectedSection);
    }, [dopData, selectedSection]);

    const handleSectionChange = (event: SelectChangeEvent<number | 'all'>) => {
        const value = event.target.value;
        setSelectedSection(value === 'all' ? 'all' : Number(value));
        setPage(0); // Сбрасываем страницу при смене секции
    };


    // Проверяет sessionStorage и обновляет ОСНОВНУЮ таблицу (data / filteredData)
    useEffect(() => {
        const handleNewRabota = async () => {
            const shouldSelect = sessionStorage.getItem('shouldSelectNewRabota');
            if (shouldSelect !== 'true') return;  // Только если флаг установлен

            try {
                console.log('Обнаружена новая работа в sessionStorage. Обновляем data...');

                // Перезагружаем данные из API
                const newDataResponse = await getRabotaByObjectId(objectIdNum);
                const newData = Array.isArray(newDataResponse) ? newDataResponse : [];
                setData(newData);

                // Обновляем filteredData по текущей секции
                const filtered = selectedSection === 'all'
                    ? newData
                    : newData.filter((item: Rabota) => item.sekciya === Number(selectedSection));
                setFilteredData(filtered);

                // Ищем и выделяем новую работу
                const storedRabotaStr = sessionStorage.getItem('newlyAddedRabota');
                if (storedRabotaStr) {
                    const newRabota: Rabota = JSON.parse(storedRabotaStr);

                    // Поиск по уникальным полям (ID родителя + ID работы + секция)
                    const found = newData.find((item: Rabota) =>
                        item.projectRabota_ID === newRabota.projectRabota_ID &&
                        item.sprRabota_Rabota_ID === newRabota.sprRabota_Rabota_ID &&
                        item.sekciya === newRabota.sekciya &&
                        item.rabotaPrim === newRabota.rabotaPrim  // Дополнительно для точности
                    );

                    if (found) {
                        setCurrentRow(found);
                        console.log('Выделена новая работа:', found);
                    } else {
                        console.warn('Новая работа не найдена в обновленных данных');
                    }

                    sessionStorage.removeItem('newlyAddedRabota');
                }

                // Очищаем флаг
                sessionStorage.removeItem('shouldSelectNewRabota');
                console.log('Данные обновлены и флаг очищен');
            } catch (err) {
                console.error('Ошибка обновления после добавления работы:', err);
                // Очищаем storage даже при ошибке
                sessionStorage.removeItem('shouldSelectNewRabota');
                sessionStorage.removeItem('newlyAddedRabota');
            }
        };

        handleNewRabota();
    }, [location, objectIdNum, selectedSection]);  // location меняется при navigate(-1)!


////////для интерактивной загрузки данных///////////////////////////////
    if (loading) {
        return (
            <StatusMessage>
            <CircularProgress />
            <Typography variant="h6" sx={{ ml: 2 }}>
                Загрузка данных...
            </Typography>
            </StatusMessage>
        );
    }
    if (error) {
        return (
            <StatusMessage>
            <Typography variant="h6" color="error">
                {error}
            </Typography>
            </StatusMessage>
        );
    }
    if (data.length === 0) {
        const fillerRow: Rabota = {
            object_ID: objectIdNum || 0,
            смета_ID: 0,
            smeta: "Нет данных",
            projectRabota_ID: 0,
            sprRabota: "Нет работ",
            rabotaPrim: "",
            edIzm_ID: 0,
            edIzm: "-",
            objemObsh: 0,
            objemDop: 0,
            dop: 0,
            sekciya: 0,
            procent: 0,
            comment: "Нет данных для отображения",
            sprRabota_Rabota_ID: 0,
            sprRabota_Rabota: ""
        };
        data.push(fillerRow);
    }

    const styleForText = {
        textDecoration: 'underline'
    }

    function onJobDeleted() {
        throw new Error("Function not implemented.");
    }

//// Основное тело страницы////////////////////////////////////
    return (
        <Paper sx={{ 
            width: '100%', 
            height: '100%', 
            overflow: 'hidden',
            display: 'flex',
            flexDirection: 'column'
        }}>
        {pageSite == 0 && (
        <>
            <Box 
                sx={{ 
                    p: '1em 3em 1em 3em', 
                    borderBottom: 1, 
                    borderColor: 'divider',
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems:'center',
                    justifyContent: 'space-between',
                    gap: '2em'

                }}>
                    <Box sx={{display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-around', gap:'1.5em'}}>
                        <Box>
                            <Typography variant="h5" gutterBottom fontSize={20}
                                sx={{
                                    fontWeight: '600',
                                    fontSize: '1em'
                                }}>
                                Комплектные работы объекта №{objectIdNum}: <text style={styleForText}> {displayName} </text>
                            </Typography>
                            <Typography variant="body2" color="text.secondary" sx={{ fontSize: 13 }}>
                                    {currentRow?.смета_ID ? 'Смета: ' + currentRow.smeta + ' ('+currentRow.смета_ID+')' : ' '} 
                                    <br/>
                                    {currentRow?.projectRabota_ID ? 'Работа: ' + currentRow.sprRabota + ' (' + currentRow.projectRabota_ID + ')' : ''} 
                            </Typography>

                            {/* <Typography variant="body2" color="text.secondary" sx={{ fontSize: 13 }}>
                                Всего записей: {data.length}
                            </Typography>
                                */}

                        </Box>
                            
                        <FormControl sx={{display: 'flex', alignItems: 'center' }}>
                            <InputLabel id="section-select-label">
                                Секция
                            </InputLabel>
                            <Select
                                labelId="section-select-label"
                                id="section-select"
                                value={selectedSection}
                                onChange={handleSectionChange}
                                label="Секция"
                                sx={{ minWidth: 120, maxHeight: 35, fontSize: 13 }}
                            >
                                <MenuItem value="all">Все секции</MenuItem>
                                {sections.map(section => (
                                    <MenuItem key={section} value={section}>
                                        Секция {section}
                                    </MenuItem>
                                ))}
                            </Select>

                            {/* Доп. инфа */}
                            <Typography variant="body2" color="text.secondary" sx={{ fontSize: 8 }}>
                                Показано: {filteredData.length} из {data.length} записей
                            </Typography>
                        </FormControl>
                    </Box>
                <Box 
                    sx={{ 
                        // p: '1em 3em 1em 3em', 
                        p: 0,
                        display: 'flex',
                        flexDirection: 'row',
                        height:'3em',
                        width: '45%',
                        alignSelf: 'end',
                        justifyContent: 'space-around',
                        alignItems: 'center'
                    }}>  
                    <Button sx={getCustomButtonStyles('success', 'medium')}
                        onClick={() => 
                            navigate(`/rabota/${objectId}/vybor`, { state: { 
                                background: location,
                                objectId: objectId,
                                sekcii: sectionsArray ? sectionsArray : [],
                                selectedSection: Number(selectedSection) ? selectedSection : 1
                            } })}
                        >
                        Добавить работу
                    </Button>

                    <Button sx={getCustomButtonStyles('danger', 'medium')}
                     onClick={handleOpenDialogDeleteRabota}>
                        Удалить работу
                    </Button>

                    <Button sx={getCustomButtonStyles('default', 'medium')}
                        onClick={handleOpenDialogAddSection}>
                        Добавить секцию
                    </Button>

                    <Button sx={getCustomButtonStyles('default', 'medium')}
                    // onClick={() => copyRabot()}
                    >
                        Копирование работ
                    </Button>
                </Box>  
            </Box>    

                {/* При выборе страницы с доп работами поменяется номер страницы page */}
            <RabotaTable 
                object_ID = {objectIdNum}
                data={filteredData} 
                currentPage={pageSite} 
                onPageChange={handlePageChange}
                onCurrentRowChange={handleCurrentRowChange}
                dopRabotaPairs={dopRabotaPairs}
                onFlagTipRabChange = {onFlagTipRabChange}
                currentRow={currentRow?? undefined}
                />
        </>
        )}
        
            {pageSite === 1 && (
            <>
                <Box 
                    sx={{ 
                        p: '1em 3em 1em 3em', 
                        borderBottom: 1, 
                        borderColor: 'divider',
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'space-between',
                        gap: '2em',
                        alignItems: 'center'
                    }}>
                    <Box
                        sx={{ 
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'start',
                        alignItems: 'center',
                        gap: '3em'
                    }}> 
                        <Box>
                            <Typography variant="h5" gutterBottom fontSize={20}
                                sx={{
                                    fontWeight: '600',
                                    fontSize: '1em'
                                }}>
                                Дополнительные работы объекта <text style={styleForText}> {displayName} </text>
                            </Typography>

                            {/* Информация о выбранной работе */}
                            {selectedRabota && (
                                <Typography variant="body2" color="text.secondary" sx={{ fontSize: 13 }}>
                                    <strong>{selectedRabota.sprRabota}</strong>
                                    { ` (${selectedRabota.projectRabota_ID})`}
                                </Typography>
                            )}
                        </Box>
                    
                        <FormControl sx={{ display: 'flex', alignItems: 'center' }}>
                            <InputLabel id="section-select-label">
                                Секция
                            </InputLabel>
                            <Select
                                labelId="section-select-label"
                                id="section-select"
                                value={selectedSection}
                                onChange={handleSectionChange}
                                label="Секция"
                                sx={{ minWidth: 120, maxHeight: 35, fontSize: 13 }}
                                >
                                <MenuItem value="all">Все секции</MenuItem>
                                {sections.map(section => (
                                    <MenuItem key={section} value={section}>
                                        Секция {section}
                                    </MenuItem>
                                ))}
                            </Select>

                        </FormControl>
                    </Box>

                    <Box 
                        sx={{ 
                            display: 'flex',
                            flexDirection: 'row',
                            alignSelf: 'center',
                            gap: '1em',
                            justifyContent: 'space-around'
                        }}>  

                        <Button sx={getCustomButtonStyles('success', 'medium')}
                            onClick={handleAddButtonClick}>
                            Добавить работу
                        </Button>

                        <Button sx={getCustomButtonStyles('danger', 'medium')}
                            onClick={handleOpenDialogDeleteRabotaMelk}>
                            Удалить работу
                        </Button>

                        <Button sx={getCustomButtonStyles('default', 'medium')}
                        onClick={handleOpenDialogAddSection}>
                            Добавить секцию
                        </Button>

                        <Button sx={getCustomButtonStyles('default', 'medium')}
                        // onClick={() => copyRabot()}
                        >
                            Копирование работ
                        </Button>
                    </Box>  
                    
                    <Button sx={getCustomButtonStyles('default', 'medium')}
                        onClick={() => {
                                setPageSite(0);
                                setSelectedRabota(null); // Очищаем выбранную работу при возврате
                            }}
                    >
                        ← Назад к таблице
                    </Button>
                </Box>   

                <DopRabotaTable 
                    object_ID = {objectIdNum!}
                    data={filteredDopRabota} 
                    selectedRabota={selectedRabota}
                    onPageChange={handlePageChange}
                    onDopRabotaChange={handleCurrentDopRabota}
                    onBackToMainTable={() => {
                        setPageSite(0);
                        setSelectedRabota(null);
                    }}
                    nameOfObject={objectName}
                    />
                
            </>)
            }

            {pageSite === 3 && (
                <>
                    <MaterialDop 
                        rabota = {selectedRabota}
                        nameOfObject = {objectName} 
                        flagTipRab = {flagTipRab}
                        open={true}
                        onClose={() => {
                            handlePageChange(1); 
                        }} 
                    /> 
                </>
            )}

            {isDialogAddSectionOpen && (
                <AddNewSectionDialog
                    open={true}
                    onClose={handleClosingDialogAddSection}
                    objectId={Number(objectId)}
                    sekcii={sectionsArray.length ? sectionsArray : []}
                    selectedSection={Number(selectedSection) || 1}
                    onSectionAdded={handleSectionAdded}
                />
            )}


            {isDialogDeleteRabotaOpen && currentRow && (
                <DeleteDialog
                    open={true}
                    onClose={handleClosingDialogDeleteRabota}
                    title={`Вы уверены, что хотите удалить работу "${currentRow.smeta}" и все подработы?`}
                    successMessage={`Работа "${currentRow.smeta}" удалена успешно`}
                    onDelete={() =>
                        deleteRabota(Number(objectId), currentRow.projectRabota_ID, currentRow.rabotaPrim)
                    }
                    onDeleted={handleJobDeleted}
                />
                )}


            {isDialogDeleteRabotaMelkOpen && currentDopRabota && (
                <DeleteDialog
                    open={true}
                    onClose={handleClosingDialogDeleteRabotaMelk}
                    title={`Вы уверены, что хотите удалить подработу "${currentDopRabota.sprRabota_Kompl}", а также все её материалы?`}
                    successMessage={`Работа "${currentDopRabota.smeta}" удалена успешно`}
                    onDelete={() =>
                        deleteMelkRabota(Number(objectId), currentDopRabota.projectRabota_ID, currentDopRabota.rabotaPrim, currentDopRabota.sprRabota_Kompl_ID)
                    }
                    onDeleted={handleJobDeleted}
                />
            )}
        </Paper>
    );
}

export default RabotaPage;
