import { Box, flex, fontStyle, fontWeight, Grid } from "@mui/system";
import logo from '../../static/img/l1.png';
import { Button } from "@mui/material";
import { Link } from "react-router-dom";


function Header () {

    return(
        <Grid 
            sx={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "space-between",
                width: "100%",
                height: "3.5em",
                fontWeight: "500",
                alignItems: "center",
                position: "fixed",
                background: "white",
                // background: "linear-gradient(to right, #f50202ff, #0d64e7ff)",
                color: "black",
                padding: '0 5em 0 5em',
                zIndex: 1000,
                boxShadow: '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)'
            }}>
                <Grid sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    gap: '4em',
                    alignItems: 'center'
                }}>
                    <Box
                    component={Link}
                        to="/"
                        sx={{
                            cursor: 'pointer',
                            '&:hover': {
                                height: '50px'
                            }
                        }}
                    >
                        <img 
                            src={logo}
                            alt="Логотип" 
                            style={{ height: '40px' }} 
                        />
                    </Box>
                    <Box 
                        component={Link}
                        to="/"
                        sx={{
                            textDecoration: 'none',
                            color: 'black',
                            cursor: 'pointer',
                            '&:hover': {
                                fontWeight: '400'
                            }
                        }}
                    >
                        Объекты
                    </Box>
                    
                </Grid>
                
                <Button 
                         component={Link}
                        to="/auth"
                    sx={{
                        backgroundColor: 'transparent',
                        color: 'black',
                        border: '2px solid #2261beff',
                        borderRadius: '6px',
                        padding: '0.3em 1.9em',
                        textTransform: 'none',
                        fontSize: '0.8em',
                        fontWeight: 600,
                        '&:hover': {
                            backgroundColor: 'var(--color-accent-hover)',
                            color: 'white',
                            borderColor: 'var(--color-accent-hover)',
                        },
                    }}
                >
                    Выйти
                </Button>   
                

        </Grid>
    )
}

export default Header;