

import API_BASE_URL from "../config";
import { Material } from "../models/Material";
import MaterialSummary from "../models/MaterialSummary";
import Rabota from "../models/Rabota";
import sprMaterial from "../models/SprMaterial";
import SprMaterial, { SelectedMaterial } from "../models/SprMaterial";

const API_URL = API_BASE_URL+"/api/materials";

export async function getObjectsWithMaterials(): Promise<number[]> {
    const response = await fetch(`${API_URL}/objects-with-materials`);
    return await response.json();
}


export async function getMaterials(objectId: number): Promise<MaterialSummary[]> {
    const response = await fetch(`${API_URL}/object/${objectId}`);

    if (!response.ok){
        throw new Error("Нельзя получить материалы");
    }
    
    return response.json();
}

export async function executeMaterialListProcedure(objectId: number, login: string): Promise<boolean> {
    const response = await fetch(`${API_URL}/execute-material-procedure`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ objectId, login }),
    });

    if (!response.ok) {
        throw new Error("Не удалось выполнить процедуру подготовки данных");
    }
    
    const result = await response.json();
    return result.success;
}


