import React, { useState } from "react";
import { Dialog, DialogTitle, DialogContent, DialogActions, Button } from "@mui/material";
import Swal from "sweetalert2";
import { getCustomButtonStyles } from "../utils/buttonsHelper";

interface DeleteDialogProps {
  open: boolean;
  onClose: () => void;
  onDelete: () => Promise<boolean>; 
  title: string;                    
  successMessage: string;           
  errorMessage?: string;  
  onDeleted?: () => void;          
}

const DeleteDialog: React.FC<DeleteDialogProps> = ({
  open,
  onClose,
  onDelete,
  title,
  successMessage,
  errorMessage = "Ошибка при удалении",
  onDeleted,
}) => {
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async () => {
    setIsSubmitting(true);
    try{
        const success = await onDelete();
        console.log('success=', success);
        if (success) {
            await Swal.fire({
            title: "Успешно",
            text: successMessage,
            icon: "success",
            confirmButtonText: "OK",
            customClass: {
                container: "swal2-high-zindex",
            },
            didOpen: () => {
                const popup = Swal.getPopup();
                if (popup) popup.style.zIndex = "9999";
                const container = Swal.getContainer();
                if (container) container.style.zIndex = "9999";
            },
            });
            onClose();
            if (onDeleted) onDeleted();
        } 
    } catch (error){
        await Swal.fire({
            title: "Ошибка",
            text: errorMessage,
            icon: "error",
            confirmButtonText: "OK",
            customClass: 
            {
                container: "swal2-high-zindex",
            },
            didOpen: () => {
                const popup = Swal.getPopup();
                if (popup) popup.style.zIndex = "9999";
                const container = Swal.getContainer();
                if (container) container.style.zIndex = "9999";
            },
        });
    } finally {
        setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth="sm">
      <DialogTitle>{title}</DialogTitle>
      <DialogContent></DialogContent>
      <DialogActions sx={{ display: "flex", justifyContent: "space-between", p: "0 2em 1em 2em" }}>
        <Button sx={getCustomButtonStyles("default", "medium")} onClick={onClose} disabled={isSubmitting}>
          Выйти
        </Button>
        <Button sx={getCustomButtonStyles("default", "medium")} onClick={handleSubmit} disabled={isSubmitting}>
          Удалить
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default DeleteDialog;
