import API_BASE_URL from "../config";
import { IRabotaResponse } from "../interfaces/IRabotaResponse";
import Rabota from "../models/Rabota";
import sprRabota from "../models/SprRabota";
import { getDopRabotaByObjectId } from "./dopRabotaService";


const API_URL = API_BASE_URL+"/api/Rabota";

export async function getRabotaByObjectId(objectId: number): Promise<Rabota[]> {
  const response = await fetch(`${API_URL}/object/${objectId}`);

    if (!response.ok){
      throw new Error("Не удалось получить сведения");
  }

  const result: IRabotaResponse = await response.json();
  console.log('Ответ сервера:', result.data );
  
  // Возвращаем только массив данных
  return result.data || [];
}



// Функция для получаения работ и смет, у которых существуют доп. работы
export async function isExistDopRabota(objectId: number): Promise<Array<{смета_ID: number, projectRabota_ID: number}>> {

  const data =  getDopRabotaByObjectId(objectId);
  
  if (!data) {
      return [];
  }
  
  const uniquePairs = new Set<string>();
  const result: Array<{смета_ID: number, projectRabota_ID: number}> = [];
  
  for (const item of await data) {
      if (item && item.смета_ID !== undefined && item.projectRabota_ID !== undefined) {
          const pairKey = `${item.смета_ID}_${item.projectRabota_ID}`;
          
          if (!uniquePairs.has(pairKey)) {
              uniquePairs.add(pairKey);
              result.push({
                  смета_ID: item.смета_ID,
                  projectRabota_ID: item.projectRabota_ID
              });
          }
      }
  }
  
  console.log('Результат получения пар с доп данными',result);
  return result;
}

export const checkRabotaExists = async (
  objectId: number, 
  projectRabotaId: number, 
  rabotaPrim: string, 
  sekcii: number[]) => {
  try {
    for (const sekc of sekcii) {
      const response = await fetch(`${API_URL}/rabotaIsExist`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          objectId: objectId,
          projectRabotaId: projectRabotaId,
          rabotaPrim: rabotaPrim,
          sekc: sekc
        })
      });

      if (!response.ok) {
        throw new Error('Ошибка запроса');
      }

      const exists = await response.json();
      if (exists === true) {
        console.log(`Секция ${sekc}: запись уже существует`);
        return true; // останавливаем цикл и выходим
      }
    }
    return false;
  } catch (error) {
    console.error('Ошибка:', error);
    return false;
  }
};


export const addRabota = async (
    objectId: number, 
    smetaId: number, 
    smeta: string, 
    sprRabota: string, 
    projectRabotaId: number, 
    rabotaPrim: string, 
    sekcii: number[],
    edIzmId?: number, 
    edIzm?: string, 
    ) => 
{
  try {
      const requestBody: any = {
      objectId,
      smetaId,
      smeta,
      sprRabota,
      projectRabotaId,
      rabotaPrim,
      sekcii,
      edIzmId,
      edIzm
    };
    console.log('Отправляем JSON:', requestBody);

    const response = await fetch(`${API_URL}/addrabota`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(requestBody),
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error('Ошибка запроса:', errorText);
      throw new Error(`Ошибка запроса: ${response.status}`);
    }

    const exists = await response.json();
    return exists; // true или false
  } catch (error) {
    console.error('Ошибка:', error);
    return false;
  }
};


export const deleteRabota = async (
  objectId: number,
  projectRabotaId: number,
  rabotaPrim: string
): Promise<boolean> => {
  try {
    const requestBody = { objectId, projectRabotaId, rabotaPrim };
    console.log('requestBody=', requestBody);

    const response = await fetch(`${API_URL}/DeleteRabota`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(requestBody),
    });

    console.log('response: ', response);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || "Failed to delete job");
    }

    const data = await response.json();
    console.log("Ответ сервера:", data);

    return data === true || data === "true";
  } catch (error) {
    console.error("Ошибка:", error);
    return false;
  }
};


