import { Box } from "@mui/material";

    const StatusMessage = ({ children, minHeight = "200px" }: { 
        children: React.ReactNode; 
        minHeight?: string; 
    }) => (
    <Box 
        display="flex" 
        justifyContent="center" 
        alignItems="center" 
        minHeight={minHeight}
    >
        {children}
    </Box>
    );

export default StatusMessage;