import { 
  Box, 
  Button, 
  Checkbox, 
  FormControlLabel, 
  FormControl,
  InputLabel, 
  MenuItem, 
  Select, 
  TextField, 
  Typography,
  Paper,
  Grid, 
  Stack,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton
} from '@mui/material';
import { IDopMaterialTableProps } from '../../interfaces/IDopMaterialTableProps';
import { useEffect, useState } from 'react';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import Rabota from '../../models/Rabota';
import MaterialTable from '../tables/materials-table';
import CloseIcon from '@mui/icons-material/Close';
import { getSprRzch } from '../../services/spravochnikService';
import SprRzch from '../../models/SprRzch';
import DopRab from '../../models/DopRab';
import { getDopRab, getFilteredDopRab } from '../../services/normativService';
import RubricatorDialog from '../../dialogs/materials/newObjem-dialog';
import { change_data_osnovnye, saveMaterial, saveMaterialDop, saveToTmpMat, updateDopRab } from '../../services/materialService';
import sprMaterial, { SelectedMaterial } from '../../models/SprMaterial';
import { change_data } from '../../services/materialDopService';
import { Material } from '../../models/Material';
import SprMaterial from '../../models/SprMaterial';
import { getEtajNumber } from '../../services/httpService';


const MaterialDialog: React.FC<IDopMaterialTableProps & { 
  open: boolean; onClose: () => void }> = ({ 
  rabota: propData,
  nameOfObject,
  flagTipRab,
  open,
  onClose,
  etaj,
  etajValue,
  Rabota_Kompl_ID //определяет, пришли мы из komplView1 или komplView2
}) => {

  // console.log('При загрузке flagTipRab=', flagTipRab); -- корректно
  const [selectedRabota, setSelectedRabota] = useState<Rabota>();
  const [dopRab, setDopRab] = useState<DopRab[]>([]);

  const [isDataLoaded, setIsDataLoaded] = useState(false);

  const [selectedMaterials, setSelectedMaterials] = useState<sprMaterial[]>([]);


  ////////////////////////////////////////Загрузка данных при монтировании
  useEffect(() => {
      if (propData) {
        setSelectedRabota(propData);
      };
  }, [propData]);

  useEffect(() => {
    const loadMaterials = async () => {
      if (!selectedRabota) return;

      setIsDataLoaded(false);
      
      try {

        const loadedMaterials = await change_data_osnovnye({
          Object_ID: selectedRabota.object_ID, 
          ProjectRabota_ID: selectedRabota.projectRabota_ID,  
          RabotaPrim: selectedRabota?.rabotaPrim || ' ',      
          Sekciya: selectedRabota?.sekciya,
          EtajS: etaj ? etaj : null,
          SprRabota_Kompl_ID: selectedRabota?.sprRabota_Kompl_ID || 0, 
        });

        const converted: SprMaterial[] = loadedMaterials.map(m => ({
          sprMaterial_ID: m.sprMaterial_ID,
          material_ID: m.material_ID ?? 0,
          parent_ID: 0,
          isFolder: 0,
          edIzm: m.edIzm ?? '',
          sprEdizm_ID: m.edIzm_ID ?? 0,
          cena: m.cena ?? 0,
          sprMaterial: m.sprMaterial ?? '',
          objem: m.objem ?? 0,
          comment: m.comment ?? '',
          user: m.user ?? '',
          data: m.data ?? ''
        }));
        
        console.log('loadedMaterials:',converted);
        setSelectedMaterials(converted);

      } catch (error) {
        console.error('Ошибка при загрузке уже имеющихся материалов:', error);
      } finally {
        setIsDataLoaded(true);
      }
    };

    
    if (selectedRabota) {
    loadMaterials();
    
    }
  }, [flagTipRab, selectedRabota]);


//////////////////////////////////////////////////////////////////////////
  const getCurrentUser = (): string => {
    return localStorage.getItem('userLogin') || 'aalihacheva';
  };

  //////////////////////////////////////////////////////////////////////////////

  const handleSelectedMaterialsSet = (selectedMaterials: sprMaterial[]) => {
  setSelectedMaterials(selectedMaterials);
  };

  const handleClose = () => {
  onClose();
  };

////////////// Сохранение данных (аналог Кнопка12_Click)///////////////////////////
 const handleSave = async () => {

    const objectId = selectedRabota?.object_ID;
    const projectRabotaId = selectedRabota?.projectRabota_ID;
    console.log('Сохранение данных дополнительных материалов',{objectId,projectRabotaId});
    if (!objectId || !projectRabotaId) {
      alert("Не все обязательные поля заполнены!");
      return;
    }

    try {

      // 1. Проверяем и обновляем DopRab (аналог первой части Кнопка12_Click)
      // Здесь заносится информация об обосновании, доп.объеме, ответственном и дате.
      // const dopRabUpdated = await updateDopRab({
      //   objectId,
      //   projectRabotaId,
      //   rabotaPrim: selectedRabota?.rabotaPrim || ' ',
      //   sprRzchId: parseInt(responsible),
      //   data: selectedDate,
      //   objemDop: objemDop || 0,
      //   obosn: obosn || '',
      //   zatr: 0
      // });

      // if (!dopRabUpdated) {
      //   alert("Отсутствуют данные в таблице дополнительных объемов работ <DopRab>!");
      //   return;
      // }
      const etajS_ID = await getEtajNumber(etaj);
      if (selectedMaterials.length>0){
        // 2. Сохраняем выбранные материалы в Material_dop (аналог второй части Кнопка12_Click)
        await saveMaterial({
          materials: selectedMaterials,
          objectId,
          projectRabotaId,
          sekciya: selectedRabota?.sekciya,
          etajS: etaj,
          etajPo_ID: 0,
          etajS_ID,
          rabotaPrim: selectedRabota?.rabotaPrim || ' ',
          rabotaKomplId: selectedRabota?.sprRabota_Kompl_ID || 0,
          user: getCurrentUser()
        });

        alert("Данные о работе успешно сохранены!");
        onClose();
      }
  
      
    } catch (error) {
      console.error('Ошибка сохранения:', error);
      alert("Ошибка при сохранении данных!");
    }
  };

  
  return (
    

    <Dialog
      open={open}
      onClose={handleClose}
      maxWidth="lg"
      fullWidth
      PaperProps={{
        sx: {
          borderRadius: 2,
          minHeight: '60vh',
          width: '70%'
        }
      }}
    >
      <DialogTitle sx={{ 
        m: 0, 
        p: 2,
        backgroundColor: 'var(--color-accent-active)',
        color: 'white'
      }}>
        <Typography variant="h6" component="div">
          Выбор материалов
        </Typography>
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: 'white',
          }}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>

      <DialogContent dividers sx={{ p: 3 }}>
        {/* Информация об объекте и работе */}
        <Stack spacing={1} sx={{ mb: 1 }}>
          <Box sx={{display:'flex', flexDirection:'row', gap:'3em'}}>
            <Typography variant="subtitle1" color="text.secondary">
              Объект{selectedRabota?.object_ID ? ` №${selectedRabota.object_ID}` : ''}:
              <Typography component="span" fontWeight="600" sx={{ ml: 1 }}>
                {nameOfObject || 'Не выбран'}
              </Typography>
            </Typography>
          
            <Typography variant="subtitle1" color="text.secondary">
              Смета:
              <Typography component="span" fontWeight="600" sx={{ ml: 1 }}>
                {selectedRabota?.smeta || 'Не выбрана'}
              </Typography>
            </Typography>
          </Box>

          <Box>
            <Typography variant="subtitle1" color="text.secondary">
              Работа:
              <Typography component="span" fontWeight="600" sx={{ ml: 1 }}>
                {selectedRabota?.sprRabota || 'Не выбрана'}
                {selectedRabota?.sprRabota_Kompl && ` - ${selectedRabota.sprRabota_Kompl}`}
              </Typography>
            </Typography>
          </Box>


          <Box sx={{display:'flex', flexDirection:'row', gap:'3em'}}>
            <Typography variant="subtitle1" color="text.secondary">
              Секция: <u><strong>{selectedRabota?.sekciya}</strong></u>
            </Typography>
          
            <Typography variant="subtitle1" color="text.secondary">
              <u><strong>{etaj? etaj : 'не выбран'} </strong></u>
            </Typography>
          </Box>

        </Stack>

        <Divider sx={{ my: 2 }} />

        {/* Объемы работы*/}
        <Grid container spacing={2} alignItems="center" sx={{ mb: 3, justifyContent:'space-between' }}>
          <Grid size={{ xs: 12, sm: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Объём работы общий: <u><strong> {selectedRabota?.objemObsh}</strong></u>
            </Typography>
          </Grid>
          
          <Grid size={{ xs: 12, sm: 3 }}>
              <Typography variant="subtitle2" gutterBottom>
              Объём работы по секции: <u><strong> {selectedRabota?.objem}</strong></u>
            </Typography>
          </Grid>

          <Grid size={{ xs: 12, sm: 3 }}>
              <Typography variant="subtitle2" gutterBottom>
              Объём работы по этажу: <u><strong> {etajValue ? etajValue : 0}</strong></u>
            </Typography>
          </Grid>

        </Grid>


        {/* Таблица материалов */}
        <Box sx={{ mt: 3 }}>
          <MaterialTable 
          onSelectedMaterialsSet={handleSelectedMaterialsSet} 
          filteredMaterials={selectedMaterials}
          osnovnye={true}
          />
        </Box>
      </DialogContent>

      <DialogActions sx={{ p: 2, gap: 1 }}>
        <Button 
          onClick={handleClose}
          variant="outlined"
          color="inherit"
        >
          Отмена
        </Button>
        <Button 
          onClick={handleSave}
          variant="contained"
          sx={{
            backgroundColor: 'var(--color-accent-active)',
            '&:hover': {
              backgroundColor: 'var(--color-accent-active-dark)',
            }
          }}
        >
          Сохранить
        </Button>
      </DialogActions>

    </Dialog>
  );
    
};

export default MaterialDialog;