import API_BASE_URL from "../config";
import DopRab from "../models/DopRab";
import { parse, isSameMinute } from 'date-fns';

const API_URL = API_BASE_URL+"/api/normativ";

export async function getDopRab(
objectId: number,
projectRabotaId: number,
rabotaPrim: string
): Promise<DopRab[]> {
    const requestBody = { objectId, projectRabotaId, rabotaPrim };
    const response = await fetch(`${API_URL}/getDopRab`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(requestBody),
    });

    if (!response.ok) {
            const errorText = await response.text();
            console.error("Ошибка HTTP:", response.status, errorText);
            throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
        }
    const result = await response.json();
    console.log(result);
    return result;
}

export async function getFilteredDopRab(
    dopRab: DopRab[],
    sprRzchId: number,
    data: string,
    flagTipRab: string,
    sprRabotaKomplId?: number
): Promise<DopRab[]> {
    const searchDate = parse(data, 'dd.MM.yyyy HH:mm', new Date());

    let filtered = dopRab.filter(d => 
        d.sprRzch_ID === sprRzchId &&
        isSameMinute(new Date(d.data!), searchDate)
    );
   
    if (flagTipRab === "Мелкая" && sprRabotaKomplId) {
        filtered = filtered.filter(d => d.sprRabota_Kompl_ID === sprRabotaKomplId);
    }
    
    return filtered;
}