export function getCurrentUserFromSession(): string {
    try {
        // Попробуем получить из sessionStorage
        const userFromSession =  localStorage.getItem('userLogin') 
        if (userFromSession) {
            return userFromSession;
        }
        
        const userFromLocal = localStorage.getItem('userLogin') 
        if (userFromLocal) {
            return userFromLocal;
        }
        
        const userFromCookie = getCookie('userLogin');
        if (userFromCookie) {
            return userFromCookie;
        }
        
        return 'aalihacheva'; 
    } catch (error) {
        console.error('Ошибка получения пользователя из сессии:', error);
        return 'aalihacheva';
    }
}

function getCookie(name: string): string {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) {
        return parts.pop()?.split(';').shift() || '';
    }
    return '';
}