import API_BASE_URL from "../config";
import ObjectEntity  from "../models/ObjectEntity";


const API_URL = API_BASE_URL+"/api/object";


export async function getObjects(username: string): Promise<ObjectEntity[]> {
    const response = await fetch(`${API_URL}/${username}`);
    if (!response.ok){
        throw new Error("Нельзя получить объекты");
    }
    return response.json();
}


export async function getObjectById(objectId: number): Promise<ObjectEntity> {
    const response = await fetch(`${API_URL}/by-id/${objectId}`);
    if (!response.ok){
        throw new Error("Не удалось получить объект");
    }
    return response.json();
}

