import { border, height, minHeight } from "@mui/system";

// utils/buttonHelpers.ts
export const getCustomButtonStyles = (
  colorVariant: 'default' | 'success' | 'danger' = 'default', 
  size: 'small' | 'medium' | 'large' = 'medium'
) => ({
  backgroundColor: 'transparent',
  color: 'black',
  border: '2px solid #2261beff',
  borderRadius: '6px',
  padding: '0.8em 0.8em',
  minHeight: '1em',
  textTransform: 'none' as const,
  fontSize: '0.75em',
  fontWeight: 500,
  lineHeight: '1em',
  ...(size === 'small' && { width: '2em', padding: '0', border:'0px' }),
  ...(size === 'medium' && { width: '12em',height:'35px'}),
  ...(size === 'large' && { width: '14em' }),
  '&:hover': {
    backgroundColor: 
      colorVariant === 'success' ? '#6fda3dff' :
      colorVariant === 'danger' ? '#f02020ff' : 'var(--color-accent-hover)',
    color: colorVariant === 'success' ? 'black' : 'white',
    borderColor: 
      colorVariant === 'success' ? '#6fda3db2' :
      colorVariant === 'danger' ? '#f0202065' : 'var(--color-accent-hover)',
  }
});