const API_URL = "http://localhost:5189/api/";

export async function isSectionExist(
    objectId: number,
    sectionNum: number): 
    Promise<boolean>{

    const response = await fetch(`${API_URL}sections/findSection`,{
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ objectId, sectionNum }),
    });
    
    if (!response.ok){
        throw new Error("Не удалось получить сведения");
    }

    return await response.json();
 }


export async function addSection(objectId: number, sectionNum: number): Promise<{ Success: boolean; Sections: number[] }> {
  const response = await fetch(`${API_URL}sections/addSection`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ objectId, sectionNum }),
  });

  if (!response.ok){
        throw new Error("Не удалось получить сведения");
    }
  return await response.json();
}

export async function etajNalichie(arg: number): Promise<boolean> {
 const validValues = [514, 588, 609, 1374, 1376, 1664, 1665, 1666, 1667, 1668, 1669, 2230, 2231, 2233, 2236];
 return validValues.includes(arg);
}

export async function doubleClick(
  rabota: Rabota,
  projectRabotaId: number,
  sprRabota: string,
  sekcia: number,
  activeControl: string,
  activeControlValue: number | string | undefined
): Promise<boolean | null> {
    
    if (await etajNalichie(rabota.projectRabota_ID) === false){
      alert(`Работа '${rabota.sprRabota}' не разбивается по этажам! Выберите поле "Объем раб.!"`);
      return null;
    }

    if (!isNumeric(activeControlValue)) {
      alert("Перед занесением материалов необходимо завести объем работы");
      return null;
    }
    // Определяем название этажа по активному контролу
    let etaj: string;
    if (activeControl === "tehEtaj") {
      etaj = "Тех. этаж";
    } else if (activeControl === "podval") {
      etaj = "Подвал";
    } else if (activeControl === "cherdak") {
      etaj = "Чердак";
    } else if (activeControl.startsWith("etaj")){
      const etajNumber = activeControl.replace("etaj", "");
      etaj = `Этаж ${etajNumber}`;
    } else {etaj = "не выбран"}

    const userConfirmed = window.confirm(`Работа '${sprRabota}' секция - '${sekcia}' этаж '${etaj}'`);
    if (userConfirmed) {
        try {
          const requestBody = {
            objectId: rabota.object_ID,
            projectRabotaId: rabota.projectRabota_ID
          };

          const response = await fetch(`${API_URL}rabota/hasVvodRabota`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(requestBody),
          });

          if (!response.ok) {
            throw new Error('Ошибка при выполнении запроса');
          }

          const result = await response.json();
          console.log('Результат:', result);
          
          return result;
          
      } catch (error) {
          console.error('Ошибка:', error);
          alert('Произошла ошибка при выполнении операции');
          
      }
    }
    return null;
}

function isNumeric(value: any): boolean {
  if (typeof value === 'number') return true;
  if (typeof value !== 'string') return false;
  return !isNaN(parseFloat(value)) && isFinite(value as any);
}


async function calculateTotalVolume(row: Rabota | DopRabota): Promise<number> {
  console.log('Передано в calculateTotalVolume:', row);
  let total = 0;
  
  // Специальные этажи
  const specialFloors = ['podval', 'tehEtaj', 'cherdak'] as const;
  specialFloors.forEach(key => {
    const value = row[key];
    if ((typeof value === 'number' || typeof value === 'string') && value) {
      total += Number(value);
    }
  });
  
  // Обычные этажи (1-40)
  for (let i = 1; i <= 40; i++) {
    const floorKey = `etaj${i}` as keyof (Rabota | DopRabota);
    const value = row[floorKey];
    if ((typeof value === 'number' || typeof value === 'string') && value) {
      total += Number(value);
    }
  }
  console.log('Объем по секции:', total);
  return total;
}

export async function KudaZanositObjem(
  rabota: Rabota
):  Promise<{ objSekciya: number; obshObjem: number | null } | null> {

  let flag = false; //показывает, что работа разбивается по этажам
  console.log('updatedRabota=',rabota);
  if (await etajNalichie(rabota.projectRabota_ID) === false) {
    flag = true;
    return null;
  }

    let objSekciya = await calculateTotalVolume(rabota);
    
    try {
      let obshObjem = await getSumObjem(rabota);
      
      console.log('Объем по секции:', objSekciya);
      console.log('Общий объем из БД:', obshObjem);

      if (objSekciya!==obshObjem ){
            // await updateObjem(objSekciya, rabota);
            rabota.objem = objSekciya;
      } else if (flag===false) {
        objSekciya = rabota.objem ? rabota.objem : 0;
      }

      return {
        objSekciya,
        obshObjem
      };
      
    } catch (error) {
      console.error('Ошибка при получении общего объема:', error);
      return {
        objSekciya,
        obshObjem: 0
      };
    }
  
}






