export async function ObjSekciya_AfterUpdate(
  rabota: Rabota
): Promise<boolean> {
      let objem = await getSumObjem(rabota);
      let res;
      if (objem !== null) {
        res = updateObjem(objem, rabota);
      } else {
        res = updateObjem(0, rabota);
      }
      return res;
}

