export async function parseEtaj (activeControl: string) {

  let etaj: string;
    if (activeControl === "tehEtaj") {
      etaj = "Тех. этаж";
    } else if (activeControl === "podval") {
      etaj = "Подвал";
    } else if (activeControl === "cherdak") {
      etaj = "Чердак";
    } else if (activeControl.startsWith("etaj")){
      const etajNumber = activeControl.replace("etaj", "");
      etaj = `${etajNumber}`;
    } else {etaj = "не выбран"}

    return etaj;
  
}

export function getEtajNumber(etajName: string | undefined): number | null {
  if (!etajName) return null;

  const name = etajName.trim().toLowerCase();

  switch (name) {
    case "подвал":
      return -1;
    case "тех. этаж":
      return -2;
    case "чердак":
      return 0;
    default:
      return parseInt(name);
  }
}

export function getEtajField(etaj: number): string {

  switch (etaj) {
    case -2:
      return 'TehEtaj';
    case -1:
      return 'Podval';
    case 0:
      return 'Cherdak';
    default:
      return etaj.toString();
  }
}

