import { Button, Dialog, DialogActions, DialogContent, DialogTitle, MenuItem, Paper, Select, SelectChangeEvent, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@mui/material";
import { getCustomButtonStyles } from "../../utils/buttonsHelper";
import { useNavigate, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { fetchChildSprRabota, getSprRabota } from "../../services/sprRabotaService";
import sprRabota from "../../models/SprRabota";
import { height } from "@mui/system";
import { addRabota, checkRabotaExists } from "../../services/rabotaService";
import Swal from 'sweetalert2';
import Rabota from "../../models/Rabota";


interface VyborFormProps {
  open: boolean;
  onClose: () => void;
  objectId: number;
  sekcii: number[];
  selectedSection: number;
}

const VyborForm: React.FC<VyborFormProps> = ({ open, onClose, objectId, sekcii, selectedSection }) => {
    const [selectedId, setSelectedId] = useState<number | null>(null);
    const [dropdownValues, setDropdownValues] = useState<{ [key: number]: string }>({});
    const [sprRabota, setSprRabota] = useState<sprRabota[]>([]);
    const [showParents, setShowParents] = useState(true);
    const [childWorks, setChildWorks] = useState<sprRabota[]>([]);
    const [selectedParentId, setSelectedParentId] = useState<number | null>(null);
    const [selectedPrim, setSelectedPrim] = useState("");
    const [parentRow, setParentRow] = useState<sprRabota>();
    const navigate = useNavigate();
    useEffect(() => {
        const fetchData = async () => {
            const sr = await getSprRabota();
            if (sr) {
                setSprRabota(sr);
            }
        };

        fetchData();
    }, []);

    const handleRowDoubleClick = async (row: sprRabota) => {
        // Получение дочерних работ
        setParentRow(row);
        const parentId = Number(row.sprRabota_ID);
        console.log('parentId = ', parentId, 'row=', row);
        const children = await fetchChildSprRabota(parentId);
        if (children.length > 0){
            setChildWorks(children);
            setSelectedParentId(parentId);
            setShowParents(false);
        }
    }


    // Кнопка "Добавить работу"
    const handleAddButtonClick = async (row: sprRabota) => {
        // console.log('Подработа:', row);
       

        const request =  await Swal.fire({
            title: 'Выбор действия',
            text: 'Добавить по всем секциям или только по текущей секции?',
            icon: 'question',
            showCancelButton: true,
            showDenyButton: true,
            confirmButtonText: 'Все секции',
            denyButtonText: 'Текущая секция',
            cancelButtonText: 'Отмена',
            reverseButtons: false,
            customClass: {
                container: 'swal2-high-zindex'
            },
            didOpen: () => {
                const popup = Swal.getPopup();
                if (popup) {
                    popup.style.zIndex = '9999';
                }
                const container = Swal.getContainer();
                if (container) {
                    container.style.zIndex = '9999'; 
                }
            }
        });

        
        // Добавление во все секции
        if (request.isConfirmed && parentRow) {
            if (sekcii.length===0){
                sekcii=[1];
            }
        }
        // Добавление только в текущую секцию 
        else if (request.isDenied && parentRow) {
            console.log("Текущая секция");
            sekcii = [];
            sekcii.push(selectedSection);
        }


        if ((request.isConfirmed || request.isDenied) && parentRow){
            let result = await checkRabotaExists(objectId, row.projectRabota_ID, selectedPrim, sekcii);
            if (result) {
                alert(`Работа: ${row.name} уже присутствует в списке работ!`)
                return;
            }
            let success = await addRabota(
                objectId, 
                parentRow.sprRabota_ID, 
                parentRow.name, 
                row.name, 
                row.projectRabota_ID, 
                selectedPrim, 
                sekcii,
                row.sprEdIzm_ID, 
                row.edIzm)

            if (success) {
                onClose(); 

                const rabota = new Rabota();
                rabota.object_ID = objectId;
                rabota.sprRabota_Rabota_ID = parentRow.sprRabota_ID;
                rabota.sprRabota_Rabota = parentRow.name;
                rabota.projectRabota_ID = row.projectRabota_ID;
                rabota.sprRabota = row.name;
                rabota.rabotaPrim = selectedPrim;
                rabota.sekciya = sekcii[0];
                rabota.edIzm_ID = row.sprEdIzm_ID;
                rabota.edIzm = row.edIzm;

                
                sessionStorage.setItem('shouldSelectNewRabota', 'true');
                sessionStorage.setItem('newlyAddedRabota', JSON.stringify(rabota));

                navigate(-1);
            } else {
                alert("Ошибка при добавлении работы");
            }
        }
    }

// && request.dismiss === Swal.DismissReason.cancel
    const handleBack = () => {
        setShowParents(true);
        setChildWorks([]);
        setSelectedParentId(null);
    }

    const displayData = showParents ? sprRabota : childWorks;

return (
    <Dialog open={open} onClose={onClose} fullWidth>
        <DialogTitle> 
            {showParents ? "Выбор работы" : `Дочерние работы`}
        </DialogTitle>
        <DialogContent>
            <TableContainer component={Paper} sx={{ maxHeight: 400 }}>
                <Table size="small">
                {/* <TableHead>
                    <TableRow>
                    <TableCell>Работа</TableCell>
                    <TableCell>Выбор</TableCell>
                    <TableCell>Действие</TableCell>
                    </TableRow>
                </TableHead> */}
                <TableBody>
                    {
                    displayData.map((r) => (
                    <TableRow
                        key={r.sprRabota_ID}
                        onClick={() => showParents && handleRowDoubleClick(r)}
                        sx={{ 
                            height: "0.7em", 
                            cursor: "pointer",
                            '&:hover': {
                            backgroundColor: '#e8eaf0ff'
                        }
                        }}
                        >
                        <TableCell>{!showParents && "↳ "}{r.name}</TableCell>
                        <TableCell>
                        {!showParents &&
                        <Select
                            // value={dropdownValues[r.sprRabota_ID] || ""}
                            onChange={(e: SelectChangeEvent<string>) => setSelectedPrim(e.target.value)}
                            disabled={showParents}
                            size="small"
                            sx = {{height:'1.5em', minWidth:'6.5em',fontSize: '1em'}}
                            >
                            <MenuItem value=""></MenuItem>
                            <MenuItem value="ТП">ТП</MenuItem>
                            <MenuItem value="Гараж">Гараж</MenuItem>
                        </Select>}
                        </TableCell>
                        <TableCell>
                        {!showParents && <Button sx={getCustomButtonStyles('default', 'small')}
                            onClick={() => handleAddButtonClick(r)}
                            disabled={showParents}
                            size="small"
                            >
                            📑
                        </Button>}
                        </TableCell>
                    </TableRow>
                    ))}
                </TableBody>
                </Table>
            </TableContainer>
        </DialogContent>
        <DialogActions sx={{display:'flex', justifyContent:'space-between', p:'0em 2em 1em 2em', height:'3em'}}>
            <Button sx={getCustomButtonStyles('default', 'medium')}
                onClick={showParents ? onClose : handleBack}
                >
                {showParents ? "Выход" : "Назад"}
            </Button>
            <Button sx={getCustomButtonStyles('default', 'medium')}
                disabled={showParents}
                // onClick={handleSave}
                >
                Сохранить
            </Button>
        </DialogActions>
    </Dialog>
)}

export default VyborForm;