interface NewSectionDialogProps {
  open: boolean;
  onClose: () => void;
  objectId: number;
  sekcii: number[];
  selectedSection: number;
  onSectionAdded: (selectedSection: number) => void; 
}

const AddNewSectionDialog: React.FC<NewSectionDialogProps> = ({
  open,
  onClose,
  objectId,
  sekcii,
  selectedSection,
  onSectionAdded,
}) => {
    const [sectionNumber, setSectionNumber] = useState("");
    const [error, setError] = useState("");

    const handleSubmit = async () => {
      if (!sectionNumber || isNaN(Number(sectionNumber)) || Number(sectionNumber) <= 0) {
        setError("Пожалуйста, введите корректное значение");
        return;
      }

      const sectionNum = Number(sectionNumber);
      const exists = await isSectionExist(objectId, sectionNum);
      console.log('Проверяем секцию...');
      if (exists) {
        await Swal.fire({
          title: "Ошибка",
          text: `Секция №${sectionNum} уже существует в базе данных`,
          icon: "error",
          confirmButtonText: "Ок",
          customClass: {
            container: "swal2-high-zindex",
          },
          didOpen: () => {
            const popup = Swal.getPopup();
            if (popup) popup.style.zIndex = "9999";
            const container = Swal.getContainer();
            if (container) container.style.zIndex = "9999";
          },
        });
        return;
      }

      const success = await addSection(objectId, sectionNum);
      if (success) {
        // const maxSection = await getMaxSection(objectId);
        // let newSections = [];
        // if (maxSection >= 1) {
        //   newSections = Array.from({ length: maxSection }, (_, i) => i + 1);
        // } else {
        //   newSections = [1];
        // }
        // setSectionNumber("");
        // setError("");
        // onClose();
        await Swal.fire({
          title: "Успех",
          text: `Секция №${sectionNum} успешно добавлена`,
          icon: "success",
          confirmButtonText: "OK",
          customClass: {
            container: "swal2-high-zindex",
          },
          didOpen: () => {
            const popup = Swal.getPopup();
            if (popup) popup.style.zIndex = "9999";
            const container = Swal.getContainer();
            if (container) container.style.zIndex = "9999";
          },
           
        });
        onSectionAdded(sectionNum);
      } else {
        setError("Произошла ошибка");
      }
    };


    const handleClose = () => {
      setSectionNumber("");
      setError("");
      onClose();
    };

    return (
      <Dialog 
      open={open}
      onClose={handleClose} 
      fullWidth maxWidth="sm">
        <DialogTitle>Введите номер новой секции</DialogTitle>
        <DialogContent>
          <TextField
            label="Номер секции"
            type="number"
            value={sectionNumber}
            onChange={(e) => setSectionNumber(e.target.value)}
            fullWidth
            margin="normal"
            error={!!error}
            helperText={error}
            inputProps={{ min: 1, max: 15 }}
          />

        </DialogContent>
        <DialogActions sx={{ display: "flex", justifyContent: "space-between", p: "0em 2em 1em 2em" }}>
          <Button sx={getCustomButtonStyles("default", "medium")} 
              onClick={handleClose}>
            Выйти
          </Button>
          <Button sx={getCustomButtonStyles("default", "medium")} 
              onClick={handleSubmit}>
            Добавить секцию
          </Button>
        </DialogActions>
      </Dialog>
    );
  };

export default AddNewSectionDialog