import { Button, Dialog, DialogActions, DialogContent, DialogTitle, MenuItem, Paper, Select, SelectChangeEvent, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@mui/material";
import { getCustomButtonStyles } from "../../utils/buttonsHelper";
import { useNavigate, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { addMelkRabota, checkMelkRabotaExists, fetchChildSprRabota, fetchSprRabotaMelk, getSprRabota } from "../../services/sprRabotaService";
import sprRabota from "../../models/SprRabota";
import { height } from "@mui/system";
import { addRabota, checkRabotaExists } from "../../services/rabotaService";
import Swal from 'sweetalert2';
import Rabota from "../../models/Rabota";


interface VyborFormProps {
  open: boolean;
  onClose: () => void;
  objectId: number;
  sekcii: number[];
  selectedSection: number;
  selectedRabota: Rabota | null;
}

const VyborMelkForm: React.FC<VyborFormProps> = ({ 
    open, 
    onClose, 
    objectId, 
    sekcii, 
    selectedSection, 
    selectedRabota
}) => {
    const navigate = useNavigate();
    const [sprRabotamelk, setSprRabotaMelk] = useState<sprRabota[]>([]);
    const [selectedPrim, setSelectedPrim] = useState("");

    useEffect(() => {
        const fetchData = async () => {
            if (selectedRabota){
                const sr = await fetchSprRabotaMelk(selectedRabota.projectRabota_ID);
                if (sr) {
                    setSprRabotaMelk(sr);
                }
            
        };}
        fetchData();
    }, []);


    // Кнопка "Добавить работу"
    const handleAddButtonClick = async (row: sprRabota) => {
        // console.log('Подработа:', row);
        // console.log('Текущая работа:', selectedRabota);
       
        const createNewRabotaFromAdded = (row: sprRabota, originalRabota: Rabota): Rabota => {
        return {
            ...originalRabota,
            projectRabota_ID: row.projectRabota_ID,
            sprRabota: row.name,
            sprRabota_Rabota_ID: row.sprRabotaKompl_ID,
            sprRabota_Rabota: row.name,
        };
    };

        const request =  await Swal.fire({
            title: 'Выбор действия',
            text: 'Добавить по всем секциям или только по текущей секции?',
            icon: 'question',
            showCancelButton: true,
            showDenyButton: true,
            confirmButtonText: 'Все секции',
            denyButtonText: 'Текущая секция',
            cancelButtonText: 'Отмена',
            reverseButtons: false,
            customClass: {
                container: 'swal2-high-zindex'
            },
            didOpen: () => {
                const popup = Swal.getPopup();
                if (popup) {
                    popup.style.zIndex = '9999';
                }
                const container = Swal.getContainer();
                if (container) {
                    container.style.zIndex = '9999'; 
                }
            }
        });

        let targetSections = [...sekcii];
        // Добавление во все секции
        if (request.isConfirmed) {
            if (targetSections.length === 0){
                targetSections = [1];
            }
        }
        // Добавление только в текущую секцию 
        else if (request.isDenied) {
            targetSections = [selectedSection];
        }
        console.log('row=', row, 'selectedRabota=',selectedRabota)

        if ((request.isConfirmed || request.isDenied) && row.sprRabotaKompl_ID && selectedRabota){
            let result = await checkMelkRabotaExists(objectId, row.sprRabotaKompl_ID, selectedPrim, targetSections);
            if (result) {
                alert(`Работа: ${row.name} уже присутствует в списке работ!`)
                return;
            }
            let success = await addMelkRabota(
                objectId, 
                selectedRabota.смета_ID, 
                selectedRabota.smeta, 
                selectedRabota.sprRabota, 
                row.projectRabota_ID, 
                selectedPrim, 
                targetSections,
                row.name,
                row.sprRabotaKompl_ID,
                row.sprEdIzm_ID, 
                row.edIzm)

            if (success) {
                const newRabota = createNewRabotaFromAdded(row, selectedRabota);
                console.log('Сейчас вызовется функция обновления');
                
                onClose(); 

                Swal.fire({
                title: 'Успешно!',
                text: `Работа "${row.name}" добавлена`,
                icon: 'success',
                timer: 1500,
                showConfirmButton: false
            });
                window.location.reload(); 
            } else {
                alert("Ошибка при добавлении работы");
            }
        }
    }

return (
    <Dialog open={open} onClose={onClose} fullWidth>
        <DialogTitle> Выбор допработы </DialogTitle>
        <DialogContent>
            <TableContainer component={Paper} sx={{ maxHeight: 400 }}>
                <Table size="small">
                <TableBody>
                    {
                    sprRabotamelk.map((r) => (
                    <TableRow
                        key={r.sprRabota_ID}
                        onClick={() => console.log('Данные строки:', r)}
                        sx={{ 
                            height: "0.7em", 
                            cursor: "pointer",
                            '&:hover': {
                            backgroundColor: '#e8eaf0ff'
                        }
                        }}
                        >
                        <TableCell>{r.name}</TableCell>
                        <TableCell>
                        <Select
                            onChange={(e: SelectChangeEvent<string>) => setSelectedPrim(e.target.value)}
                            size="small"
                            sx = {{height:'1.5em', minWidth:'6.5em',fontSize: '1em'}}
                            >
                            <MenuItem value=""></MenuItem>
                            <MenuItem value="ТП">ТП</MenuItem>
                            <MenuItem value="Гараж">Гараж</MenuItem>
                        </Select>
                        </TableCell>
                        <TableCell>
                        <Button sx={getCustomButtonStyles('default', 'small')}
                            onClick={() => handleAddButtonClick(r)}
                            size="small"
                            >
                            📑
                        </Button>
                        </TableCell>
                    </TableRow>
                    ))}
                </TableBody>
                </Table>
            </TableContainer>
        </DialogContent>
        <DialogActions sx={{display:'flex', justifyContent:'space-between', p:'0em 2em 1em 2em', height:'3em'}}>
            <Button sx={getCustomButtonStyles('default', 'medium')}
                onClick={onClose}>
                Выход
            </Button>
            <Button sx={getCustomButtonStyles('default', 'medium')}
                // onClick={handleSave}
                >
                Сохранить
            </Button>
        </DialogActions>
    </Dialog>
)}

export default VyborMelkForm;