import API_BASE_URL from "../config";
import RubricatorRequest from "../models/RubricatorRequest";
import RubricatorResponse from "../models/RubricatorResponse";
import SprRzch from "../models/SprRzch";

const API_URL = API_BASE_URL+"/api/rubricator";

export async function getSprRzchByNomer(nomer: string): Promise<SprRzch> {
  const response = await fetch(`${API_URL}/getSprRzchByNomer`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ nomer }),
  });

  if (!response.ok) {
    throw new Error("Не удалось получить рубрикатор");
  }
  return await response.json();
}

export async function createDopRab(request: RubricatorRequest): Promise<RubricatorResponse> {
  const response = await fetch(`${API_URL}/createDopRab`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(request),
  });

  if (!response.ok) {
    throw new Error("Не удалось создать дополнительную работу");
  }
  return await response.json();
}

export async function executeKompInsDopRab(request: RubricatorRequest): Promise<RubricatorResponse> {
  const response = await fetch(`${API_URL}/executeKompInsDopRab`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(request),
  });

  if (!response.ok) {
    throw new Error("Не удалось выполнить KompInsDopRab");
  }
  return await response.json();
}