import React, { useState, useEffect } from 'react';
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow, 
  Paper, Box, Typography, Dialog, DialogTitle, DialogContent, DialogActions,
  Button, TextField, FormControlLabel, Checkbox, Alert, IconButton
} from '@mui/material';
import { Folder, FolderOpen, Close } from '@mui/icons-material';
import SprMaterial, { SelectedMaterial } from '../../models/SprMaterial';
import {  clearUserMaterials,  loadFolderMaterials,  loadListMaterials,  saveToTmpMat, saveMaterialDop, updateDopRab, loadTmpMaterials 
} from '../../services/materialService';
import sprMaterial from '../../models/SprMaterial';
import { Material } from '../../models/Material';


interface MaterialTableProps {
  flagTipRab?: string;
  rabotaKomplId?: number;
  objectId?: number;
  projectRabotaId?: number;
  rabotaPrim?: string;
  responsible?: string;
  selectedDate?: string;
  objemDop?: number;
  obosn?: string;
  onSelectedMaterialsSet: (selectedMaterials: SprMaterial[]) => void;
  filteredMaterials: SprMaterial[];
  osnovnye: boolean;
}

const MaterialTable: React.FC<MaterialTableProps> = ({ 
  flagTipRab, 
  rabotaKomplId,
  objectId,
  projectRabotaId,
  rabotaPrim,
  responsible,
  selectedDate,
  objemDop,
  obosn,
  onSelectedMaterialsSet,
  filteredMaterials,
  osnovnye
}) => {
  const [selectedMaterials, setSelectedMaterials] = useState<SprMaterial[]>([]);
  const [showMaterialDialog, setShowMaterialDialog] = useState(false);
  const [materialType, setMaterialType] = useState<'folder' | 'list' | null>(null);
  const [availableMaterials, setAvailableMaterials] = useState<SprMaterial[]>([]);
  const [currentFolderId, setCurrentFolderId] = useState<number | null>(null);
  const [loading, setLoading] = useState(false);
  const [breadcrumbs, setBreadcrumbs] = useState<{id: number | null, name: string}[]>([{ id: null, name: 'Корень' }]);


  interface CacheItem {
    data: SprMaterial[];
    timestamp: number;
  }

  const [materialsCache, setMaterialsCache] = useState<{
    folder: CacheItem | null;
    list: CacheItem | null;
  }>({ folder: null, list: null });

  // Время жизни кэша в миллисекундах (5 минут)
  const CACHE_DURATION = 5 * 60 * 1000;
  const isCacheValid = (cacheItem: CacheItem | null): boolean => {
    if (!cacheItem) return false;
    return Date.now() - cacheItem.timestamp < CACHE_DURATION;
  };

  // Получаем логин пользователя
  const getCurrentUser = (): string => {
    return  localStorage.getItem('userLogin')  || 'aalihacheva';
  };

  // Загрузка материалов для текущей папки
  const loadMaterialsForFolder = async (folderId: number | null = null) => {
    const user = getCurrentUser();
    console.log('А передан тип', materialType);
    setLoading(true);
    
    try {
      let materials: SprMaterial[];
      
      if (currentFolderId === null) {
      // Проверяем валидность кэша
      if (materialType === 'folder' && isCacheValid(materialsCache.folder)) {
        console.log('Используем валидный кэш для folder');
        materials = materialsCache.folder!.data;
      } else if (materialType === 'list' && isCacheValid(materialsCache.list)) {
        console.log('Используем валидный кэш для list');
        materials = materialsCache.list!.data;
      } else {
        // Загружаем из БД если кэш невалиден
        if (materialType === 'folder') {
          materials = await loadFolderMaterials(user);
          setMaterialsCache(prev => ({ 
            ...prev, 
            folder: { data: materials, timestamp: Date.now() } 
          }));
        } else if (materialType === 'list') {
          materials = await loadListMaterials(user);
          setMaterialsCache(prev => ({ 
            ...prev, 
            list: { data: materials, timestamp: Date.now() } 
          }));
        } else {
          materials = [];
        }
      }
      
      setAvailableMaterials(materials);
      setCurrentFolderId(materialType === 'folder' ? 1 : 2);
    } else {
        materials = [];
        // Подпапка - загружаем из tmpMat
        // materials = await loadTmpMaterials(user, currentFolderId);
      }
      
      
    } catch (error) {
      console.error('Ошибка загрузки материалов:', error);
    } finally {
      setLoading(false);
      setMaterialType(null);
    }
  };

  const loadMaterialsByParentId = async (parentId: number): Promise<SprMaterial[]> => {
    const user = getCurrentUser();
    setCurrentFolderId(2);
    
    // Если есть кэш, ищем в нем
    if (materialsCache.list?.data) {
      console.log('Ищем в кэше по parent_ID:', parentId);
      const cachedMaterials = materialsCache.list.data.filter(
        material => material.parent_ID === parentId
      );
      
      // Если нашли материалы в кэше, возвращаем их
      if (cachedMaterials.length > 0) {
        console.log(`Найдено ${cachedMaterials.length} материалов в кэше для parent_ID: ${parentId}`);
        return cachedMaterials;
      }
    }
    
    // Если в кэше нет, загружаем из API
    console.log('Загружаем из API для parent_ID:', parentId);
    return await loadTmpMaterials(user, parentId);
};

  // Обработчик клика по таблице
  const handleTableClick = async () => {
    setShowMaterialDialog(true);
    // await loadMaterialsForFolder();
  };

  // Обработчик двойного клика по материалу (вход в папку)
  const handleMaterialDoubleClick = async (material: SprMaterial) => {
    if (material.isFolder === 1) {
      // Добавляем в хлебные крошки
      setBreadcrumbs(prev => [...prev, { id: material.sprMaterial_ID, name: material.sprMaterial }]);
      const materials = await loadMaterialsByParentId(material.sprMaterial_ID);
      setAvailableMaterials(materials);
    }
  };

  // Обработчик выбора типа материалов
  const handleMaterialTypeSelect = async (type: 'folder' | 'list') => {
    console.log('Заносится тип', type)
    setMaterialType(type);
    setBreadcrumbs([{ id: null, name: 'Корень' }]);
    setCurrentFolderId(null); 
    setAvailableMaterials([]);
  };

  

  
useEffect(()=> {
  console.log('filteredMaterials=',filteredMaterials);
  if (filteredMaterials.length>0) {
      setSelectedMaterials(filteredMaterials);
    }
    else {
      setSelectedMaterials([]);
    }
}, [filteredMaterials])

useEffect (() => {
  if (selectedMaterials.length>0) {
    onSelectedMaterialsSet(selectedMaterials);
  }
},[selectedMaterials])

useEffect(() => {
  if (materialType && currentFolderId === null) {
    loadMaterialsForFolder();
  }
}, [materialType, currentFolderId]); 

/////////////////////////////////////////////////////////////////////////////////////////

  // Обработчик выбора материала (чекбокс)
  const handleMaterialSelect = (material: SprMaterial, isSelected: boolean) => {
    if (material.isFolder === 1) {
      alert("Это не конечный материал!");
      return;
    }

    setSelectedMaterials(prev => {
      if (isSelected) {
        // Добавляем материал
        return [...prev, {
          ...material,
          objem: 0,
          comment: ''
        }];
      } else {
        // Удаляем материал
        return prev.filter(m => m.sprMaterial_ID !== material.sprMaterial_ID);
      }
    });
  };

  // Проверка выбран ли материал
  const isMaterialSelected = (sprMaterial_ID: number) => {
    return selectedMaterials.some(m => m.sprMaterial_ID === sprMaterial_ID);
  };

  // Обновление объема материала
  const handleVolumeChange = (sprMaterial_ID: number, objem: number) => {
    setSelectedMaterials(prev => 
      prev.map(material => 
        material.sprMaterial_ID === sprMaterial_ID 
          ? { ...material, objem } 
          : material
      )
    );
  };

  // Обновление примечания
  const handleNoteChange = (sprMaterial_ID: number, comment: string) => {
    setSelectedMaterials(prev => 
      prev.map(material => 
        material.sprMaterial_ID === sprMaterial_ID 
          ? { ...material, comment } 
          : material
      )
    );
  };

  // // Кнопка "Назад" в диалоге
  const handleBackClick = async () => {
     setCurrentFolderId(null); // Возвращаемся в корень
    setAvailableMaterials([]); // Очищаем материалы
    setMaterialType('folder');
  };

  // Кнопка "К корню" в диалоге
  const handleRootClick = async () => {
    setBreadcrumbs([{ id: null, name: 'Корень' }]);
    setMaterialType(null);
    setCurrentFolderId(null);
  };



  return (
    <Box sx={{ width: '100%' }}>
      <Dialog 
        open={showMaterialDialog} 
        onClose={() => {handleRootClick(); setShowMaterialDialog(false); }}
        maxWidth="lg"
        fullWidth
        PaperProps={{ sx: { maxHeight: '80vh', width: currentFolderId === null ? '30%' : '70%'} }}
      >
        <DialogTitle>
          <Box display="flex" justifyContent="space-between" alignItems="center">
            <Typography variant="h6">Выбор материалов</Typography>
            <IconButton onClick={() => setShowMaterialDialog(false)}>
              <Close />
            </IconButton>
          </Box>
          
          {/* <Box sx={{ mt: 1 }}>
            {breadcrumbs.map((crumb, index) => (
              <Button
                key={index}
                size="small"
                onClick={() => handleBreadcrumbClick(index)}
                sx={{ minWidth: 'auto', p: 0.5 }}
              >
                {crumb.name} {index < breadcrumbs.length - 1 ? '>' : ''}
              </Button>
            ))}
          </Box> */}
        </DialogTitle>
        

        <DialogContent >
          {/* Выбор типа отображения (только в корне) */}
          {currentFolderId === null && (
            <Box sx={{ display: 'flex', gap: 2, mb: 3, justifyContent: 'center' }}>
              <Button 
                variant={'outlined'}
                onClick={() => handleMaterialTypeSelect('folder')}
                disabled={loading}
              >
                Группы материалов
              </Button>
              <Button 
                variant={'contained'}
                onClick={() => handleMaterialTypeSelect('list')}
                disabled={loading}
              >
                Общий список
              </Button>
            </Box>
          )}


          {/* Кнопки навигации (если не в корне) */}
          {currentFolderId !== null && (
            <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
              <Button 
                variant="outlined" 
                onClick={handleRootClick}
              >
                Назад
              </Button>
              {currentFolderId === 2 && <Button 
                variant="outlined" 
                onClick={handleBackClick}
                startIcon={<FolderOpen />}
              >
                К группам
              </Button>
              }
            </Box>
          )}












          {loading ? (
            <Typography sx={{ textAlign: 'center' }}>Загрузка материалов...</Typography>
          )
           : currentFolderId != null && (   
            <TableContainer component={Paper} sx={{ maxHeight: 400 }}>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell padding="checkbox"></TableCell>
                    <TableCell>Наименование</TableCell>
                    <TableCell>Ед. изм.</TableCell>
                    <TableCell>Цена</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {availableMaterials.map((material) => (
                    <TableRow 
                      key={material.sprMaterial_ID}
                      onDoubleClick={() => handleMaterialDoubleClick(material)}
                      sx={{ 
                        cursor: (material.isFolder === 1) ? 'pointer' : 'default',
                        '&:hover': { backgroundColor: (material.isFolder === 1) ? '#f5f5f5' : 'inherit' }
                      }}
                    >
                      <TableCell padding="checkbox">
                        {(material.isFolder === 2) && (
                          <Checkbox
                            checked={isMaterialSelected(material.sprMaterial_ID)}
                            onChange={(e) => handleMaterialSelect(material, e.target.checked)}
                          />
                        )}
                        {(material.isFolder === 1) && <Folder sx={{ color: 'primary.main' }} />}
                      </TableCell>
                      <TableCell>
                        <Box display="flex" alignItems="center">
                          {material.sprMaterial}
                        </Box>
                      </TableCell>
                      <TableCell>{material.edIzm || '-'}</TableCell>
                      <TableCell>{material.cena || '-'}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          )}
        </DialogContent>
        {currentFolderId != null && 
        <DialogActions>
          <Button 
            onClick={() => {
              handleRootClick();
              setShowMaterialDialog(false)}}
            variant="contained"
            disabled={selectedMaterials.length === 0}
          >
            Выбрать ({selectedMaterials.length})
          </Button>
        </DialogActions>
        }
      </Dialog>












      {/* Основная таблица материалов */}
      <TableContainer 
        component={Paper} 
        sx={{ border: '1px solid #e0e0e0', cursor: 'pointer' }}
        onClick={handleTableClick}
      >
        <Table sx={{ minWidth: 650 }} size="small" aria-label="material table">
          <TableHead>
            <TableRow sx={{ backgroundColor: '#f5f5f5' }}>
              <TableCell sx={{ fontWeight: 'bold', borderRight: '1px solid #e0e0e0' }}>
                Наименование материала
              </TableCell>
              <TableCell sx={{ fontWeight: 'bold', borderRight: '1px solid #e0e0e0' }}>
                Примечание
              </TableCell>
              <TableCell sx={{ fontWeight: 'bold', borderRight: '1px solid #e0e0e0' }}>
                Ед. изм.
              </TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>
                Объем
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {selectedMaterials.length > 0 ? (
              selectedMaterials.map((material) => (
                <TableRow key={material.sprMaterial_ID}>
                  <TableCell sx={{ borderRight: '1px solid #e0e0e0' }}>
                    {material.sprMaterial}
                  </TableCell>
                  <TableCell sx={{ borderRight: '1px solid #e0e0e0' }}>
                    <TextField
                      size="small"
                      fullWidth
                      value={material.comment || ''}
                      onChange={(e) => handleNoteChange(material.sprMaterial_ID, e.target.value)}
                      onClick={(e) => e.stopPropagation()}
                    />
                  </TableCell>
                  <TableCell sx={{ borderRight: '1px solid #e0e0e0' }}>
                    {material.edIzm || '-'}
                  </TableCell>
                  <TableCell>
                    <TextField
                      size="small"
                      type="number"
                      inputProps={{ 
                        min: 0,
                        step: (material.edIzm?.trim() !== "шт" && material.edIzm?.trim() !== "тыс.ш") ? "0.5" : "1",
                        placeholder:  (material.edIzm?.trim() !== "шт" && material.edIzm?.trim() !== "тыс.ш") ? "0.0" : "0"
                      }}
                      value={material.objem || 0}
                      onChange={(e) => handleVolumeChange(material.sprMaterial_ID, parseFloat(e.target.value) || 0)}
                      onClick={(e) => e.stopPropagation()}
                    />
                  </TableCell>
                </TableRow>
              ))
            ) : (
              // Пустые строки если материалы не выбраны
              [...Array(2)].map((_, index) => (
                <TableRow key={index}>
                  <TableCell sx={{ borderRight: '1px solid #e0e0e0' }}></TableCell>
                  <TableCell sx={{ borderRight: '1px solid #e0e0e0' }}></TableCell>
                  <TableCell sx={{ borderRight: '1px solid #e0e0e0' }}></TableCell>
                  <TableCell></TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>
      
      <Typography variant="caption" sx={{ mt: 1, display: 'block', color: 'text.secondary' }}>
        Нажмите на таблицу для выбора материалов
      </Typography>

      {/* Отладочная информация */}
      {flagTipRab && (
        <Alert severity="info" sx={{ mt: 2 }}>
          Тип работы: {flagTipRab}, ID комплектации: {rabotaKomplId}
        </Alert>
      )}
    </Box>
  );
};

export default MaterialTable;