import * as React from 'react';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import { useEffect, useState } from 'react';
import { CircularProgress, Typography, Box, TextField } from '@mui/material';
import { useNavigate, useParams } from 'react-router-dom';

import DopRabota from '../../models/DopRabota';
import { IDopRabotaTableProps } from '../../interfaces/IDopRabotaTableProps';
import { getDopRabotaBySmeta } from '../../services/dopRabotaService';
import { updateKomplMainView2 } from '../../services/komplService';
import { etajNalichie, getEtajField, getEtajNumber, KudaZanositObjem_kompl1, parseEtaj } from '../../services/httpService';
import { handleFormCloseDopRabota } from '../../services/formCloseService';
import MaterialDop from '../forms/material-dop';
import { stableSort, getComparator } from '../../utils/sort-tables';
import Material from '../forms/material';
import { checkKrupnye } from '../../services/rabotaService';


///// Создание колонок таблицы /////////////////////////////////////

interface Column {
  id: keyof DopRabota;
  label: string;
  text: boolean;       //Можно ли вписать данные
  minWidth?: number;
  align?: 'right' | 'left' | 'center';
  format?: (value: any) => string;
  sortable?: boolean;   // Можно ли сортировать
  clickable?: boolean;  // Можно ли нажать
  isBreaks?: boolean; // Разбивается ли
}

type Order = 'asc' | 'desc';

// Основные колонки
const mainColumns: readonly Column[] = [
  { id: 'sprRabota_Kompl', label: 'Работа', minWidth: 200, sortable: true, text: false},
  { id: 'comment', label: 'Комментарий', minWidth: 170, text: true },
  { id: 'rabotaPrim', label: 'Работа (прим)', minWidth: 20, align: 'center', text: false },
  { id: 'edIzm', label: 'Ед. изм.', minWidth: 40, align: 'center', text: false },
  { 
    id: 'objemObsh', 
    label: 'Объем раб. общ.', 
    minWidth: 50, 
    align: 'center',
    format: (value: number) => value?.toLocaleString('ru-RU', {minimumFractionDigits: 2}) || "",
    sortable: true,
    text: false
  },
  { 
    id: 'objemDop', 
    label: 'Доп. работа', 
    minWidth: 30, 
    align: 'center',
    format: (value: number) => value?.toLocaleString('ru-RU', {minimumFractionDigits: 2}) || "",
    clickable: true,
    sortable: true,
    text: false
  },
  { id: 'procent', label: 'Процент', minWidth: 20, align: 'center', text: true  },
  { id: 'objem', label: 'Объем раб.', minWidth: 20, align: 'center', text: true, isBreaks: true  },
  // { id: 'sekciya', label: 'Секция', minWidth: 70, align: 'center' },
];

const createFloorColumns = (): Column[] => {
  const floorColumns: Column[] = [];
  
  // Специальные этажи
  floorColumns.push(
    { id: 'podval', label: 'Под.', minWidth: 20, align: 'center', format: (value: number) => value?.toLocaleString('ru-RU') || "", text: true, isBreaks: true    },
    { id: 'tehEtaj', label: 'Тех.э.', minWidth: 20, align: 'center', format: (value: number) => value?.toLocaleString('ru-RU') || "", text: true, isBreaks: true    },
    { id: 'cherdak', label: 'Чер.', minWidth: 20, align: 'center', format: (value: number) => value?.toLocaleString('ru-RU') || "", text: true, isBreaks: true    }
  );
  
  // Обычные этажи (1-40)
  for (let i = 1; i <= 40; i++) {
    const floorKey = `etaj${i}` as keyof DopRabota;
    floorColumns.push({
      id: floorKey,
      label: `${i}`,
      minWidth: 20,
      align: 'center',
      format: (value: number) => value?.toLocaleString('ru-RU') || "",
      text: true,
      isBreaks: true  
    });
  }
  

  return floorColumns;
};
const floorColumns = createFloorColumns();
const allColumns = [...mainColumns, ...floorColumns];



///////////////////////////////////////////////////////////////////////


const DopRabotaTable: React.FC<IDopRabotaTableProps> = ({ 
  object_ID,
  data: propData,
  selectedRabota, 
  onPageChange,
  onDopRabotaChange,
  onBackToMainTable,
  currentDopRabota,
  nameOfObject
}) => {
  const [localData, setLocalData] = useState<DopRabota[]>([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(25);
  // сортировка
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<keyof DopRabota>('sprRabota');
  const [selectedRow, setSelectedRow] = useState<DopRabota | null>(null); 
    
  const [editCell, setEditCell] = useState<{rowKey: string; columnId: string} | null>(null);
  
  const [isMaterialDopOpen, setIsMaterialDopOpen] = useState(false);
  const [isMaterialOpen, setIsMaterialOpen] = useState(false);
  const [selectedRabotaForDialog, setSelectedRabotaForDialog] = useState<DopRabota | null>(null);

  const [activeEtaj, setActiveEtaj] = useState("");
  const [activeEtajValue, setActiveEtajValue] = useState(0);
  const navigate = useNavigate();

/////////////////////////// м о н т и р о в а н и е//////////////////////////////////
    useEffect(() => {
    if (selectedRabota) {
      const foundRow = localData.find(
        (row) =>
          row.projectRabota_ID === selectedRabota.projectRabota_ID &&
          row.смета_ID === selectedRabota.смета_ID
      );
      if (foundRow) {
        setSelectedRow(foundRow);
      }
    }
  }, [selectedRabota, localData]);


  useEffect(() => {
    if (propData) {
        //console.log('Все доп работы', propData);
        
        if (selectedRabota) {
            getDopRabotaBySmeta(
                selectedRabota.смета_ID, 
                selectedRabota.projectRabota_ID, 
                propData
            ).then(filteredDopData => {
                //console.log('Отфильтрованные доп работы:', filteredDopData);
                setLocalData(filteredDopData);
            });
        } else {
            setLocalData(propData);
        }
    }
}, [propData, selectedRabota]);


 useEffect(() => {
    return () => {
      handleFormCloseDopRabota(propData[0].object_ID).catch(error => {
        console.error('Ошибка при автоматическом закрытии формы:', error);
      });
    };
  }, [page, propData]);


/////////////////////////////////////////////////////////////////////////////////////
    const getRowKey = (row: DopRabota) =>
    `${row.projectRabota_ID}-${row.sprRabota_Kompl_ID}-${row.sekciya}`;
  

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  const handleRequestSort = (property: keyof DopRabota) => {
    if (orderBy === property) {
    if (order === 'asc') {
      setOrder('desc');
    } else {
      setOrder('asc');
      setOrderBy('sprRabota'); 
    }
  } else {
    setOrder('asc');
    setOrderBy(property);
  }
  };

  const handleCellClick = (column: Column, row: DopRabota) => {
      setSelectedRow(row);
      //console.log('selectedRow = ', selectedRow);
      // onCurrentRowChange(row);
  
    };

  const handleCloseMaterialDop = () => {
    setIsMaterialDopOpen(false);
    setSelectedRabotaForDialog(null);
    
    if (selectedRabotaForDialog) {
      onPageChange(1, selectedRabotaForDialog);
    }
  };

  const handleCloseMaterial = () => {
    setIsMaterialOpen(false);
    setSelectedRabotaForDialog(null);
    
    if (selectedRabotaForDialog) {
      onPageChange(1, selectedRabotaForDialog);
    }
  };

  const handleDoubleClick = async (column: Column, row: DopRabota) => {
    
      if (column.clickable ) {
        // onPageChange(3, row); 
        console.log('Должно открыться окно с материалами');
        setSelectedRabotaForDialog(row);
    
        // Открываем диалог
        setIsMaterialDopOpen(true);
        localStorage.setItem('selectedRabota', JSON.stringify(row));
      }

      if (column.isBreaks === true){

        const rabotaIsBreaks = await etajNalichie(row.projectRabota_ID);
        if (rabotaIsBreaks === false && column.id !== 'objem'){
          alert(`Работа '${row.sprRabota}' не разбивается по этажам! Заносите объем в "Объем работ"`);
          return;
        }


        if (Number(row[column.id]) === null) {
          alert ('Перед занесением материалов необходимо занести объем работы');
          return;
        }

        let etaj = await parseEtaj(column.id.toString());
        let etajNumber = await getEtajNumber(etaj);
        setActiveEtaj(etaj);
        setActiveEtajValue(Number(row[column.id]));
        if (etajNumber && selectedRabota) {
          let fiidname = await getEtajField(etajNumber);

          if (fiidname != "") {
            let rst = await checkKrupnye(
              selectedRabota?.object_ID,
              selectedRabota?.projectRabota_ID,
              selectedRabota?.sekciya,
              selectedRabota?.rabotaPrim,
              fiidname
            )

            if (rst===false) {
              alert ('Занесите объем на крупную работу!');
              return;
            }
          }
        }
        console.log('Должно открыться окно с материалами');
        setSelectedRabotaForDialog(row);
        setIsMaterialOpen(true);
      }
  }
  ////////////////////////////////////////////Функции для изменения содержания ячеек
  const [tempValues, setTempValues] = useState<{[key: string]: string}>({});
  const validValues = [514, 588, 609, 1374, 1376, 1664, 1665, 1666, 1667, 1668, 1669, 2230, 2231, 2233, 2236];
  const [isDirty, setIsDirty] = useState(false); // признак изменений

  const handleCellChange = async (
    rabota:DopRabota, 
    rowKey: string, 
    columnId: string, 
    newValue: any, 
    isText: boolean
  ) => {
    let finalValue: string | number | null;  

    if (columnId!=='comment'){
        // const cleanedValue = newValue.replace(/[^\d,.-]/g, '');

        // const valueForParsing = cleanedValue.replace(',', '.');
        // const numericValue = parseFloat(valueForParsing);
        // const isNumber = !isNaN(numericValue) && isFinite(numericValue);
        
        // const hasInvalidChars = newValue !== cleanedValue;

        // Разрешаем ввод промежуточных числовых форм (включая запятую в конце)
        const validPattern = /^-?\d*(?:[.,]\d*)?$/;
        if (!validPattern.test(newValue)) {
          alert("Допускаются только цифры, запятая, точка и минус!");
          return;
        }

        if (!validValues.includes(rabota.projectRabota_ID) 
          && isText 
          && (columnId!=='objem') 
          && (columnId!=='procent')){
          alert(`Работа '${rabota.sprRabota}' не разбивается по этажам! Выберите поле "Объем раб.!"`);
          return;
        }  
        finalValue = newValue === "" ? null : newValue;
      }
      else{
        // Для комментария просто сохраняем локально, без запроса
        finalValue = newValue;
      }
      
        setTempValues(prev => ({
          ...prev,
          [`${rowKey}-${columnId}`]: newValue
        }));

      setLocalData((prev) =>
        prev.map((r) => (getRowKey(r) === rowKey ? { ...r, [columnId]: finalValue } : r))
      );

      setIsDirty(true);
  };
  
  // При потере фокуса 
  const handleCellBlur = (rowKey: string, columnId: string) => {
    
    const tempKey = `${rowKey}-${columnId}`;
    const tempValue = tempValues[tempKey];
    console.log('tempvalue=', tempValue);

    if (!tempValue || tempValue === "") {
      setEditCell(null);
      // Очищаем временное значение
      setTempValues(prev => {
        const newTemp = {...prev};
        delete newTemp[tempKey];
        return newTemp;
      });
      return;
    }

    const updatedRabota = localData.find((r) => getRowKey(r) === rowKey);

    if (updatedRabota) {
      const key = columnId as keyof DopRabota;
      if (columnId!=='comment'){
        const cleaned = tempValue.replace(',', '.');
        const numericValue = parseFloat(cleaned);
        (updatedRabota[key] as any) = isNaN(numericValue) ? null : numericValue;
      } 
      else if (columnId==='comment'){
        (updatedRabota[key] as any) = tempValue.trim();
      }


      setTimeout(async () => {
        try {
            const result = await KudaZanositObjem_kompl1(updatedRabota);
            if (result) {

              setLocalData(currentData => currentData.map(r => 
                   r.komplMainView2_ID === updatedRabota.komplMainView2_ID
                  ? { ...r, objem: result.objSekciya}
                  : r
              ));
            }

          updateKomplMainView2(updatedRabota).then((success) => {
            if (success) console.log('Данные сохранены в KomplMainView2');
          });
        } catch (error) {
          console.error(`Ошибка при обработке поля ${columnId}:`, error);
        }
      }, 0);
      
    }
    
    
    if (tempValue && (tempValue.endsWith(',') || tempValue.endsWith('.'))) {
      // Убираем trailing запятую/точку и конвертируем
      const numericValue = parseFloat(tempValue.slice(0, -1).replace(',', '.'));
      
      if (!isNaN(numericValue)) {
        setLocalData(prev => {
          return prev.map(r => {
            const key = getRowKey(r);
            if (key === rowKey) {
              return { ...r, [columnId]: numericValue };
            }
            return r;
          });
        });
      }
    }
    
    // Очищаем временное значение
    setTempValues(prev => {
      const newTemp = {...prev};
      delete newTemp[tempKey];
      return newTemp;
    });
  };
  
  //////////////////////////////////////////////////////////Начало вывода таблицы
  return (
    <Box sx={{pl:2, pr:2}}>
      <TableContainer sx={{ flex: 1, maxHeight: 'calc(100vh - 190px)' }}>
        <Table stickyHeader aria-label="Rabota table" size="small">
          <TableHead>
            <TableRow>
              {allColumns.map((column) => (
                <TableCell
                  key={column.id.toString()}
                  align={column.align}
                  style={{ 
                    fontSize: '0.75em',
                    lineHeight: '1.3em',
                    minWidth: column.minWidth,
                    fontWeight: 'bold',
                    backgroundColor: '#f5f5f5',
                    cursor: column.sortable ? 'pointer' : 'default'
                  }}
                  onClick={() => {
                    if (column.sortable) {
                      handleRequestSort(column.id);
                    }
                  }}
                >
                  {column.label}
                  {orderBy === column.id ? (order === 'asc' ? ' ⇡' : ' ⇣') : column.sortable ? ' ↕' : ''}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>


          <TableBody>
            {stableSort(localData, getComparator(order, orderBy))
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row, index) => {
                
                const isSelected = selectedRow?.komplMainView2_ID === row.komplMainView2_ID || 
                  currentDopRabota?.komplMainView2_ID === row.komplMainView2_ID;
                const rowKey = getRowKey(row);

                return (
                  <TableRow 
                    hover 
                    role="checkbox" 
                    tabIndex={-1} 
                    key={`${row.object_ID}-${row.projectRabota_ID}-${index}`}
                    sx={{ 
                      backgroundColor: isSelected ? '#e0eef8ff' : 'inherit',
                      '&:hover': {
                        backgroundColor: isSelected ? '#bbdefb' : '#f5f5f5'
                      }
                    }}
                  >
                    {allColumns.map((column) => {
                      const value = row[column.id];
                      const isEditing =  editCell?.rowKey === rowKey && editCell?.columnId === column.id;
                      // console.log(`Колонка: ${column.id}, Значение:`, value); 

                      return (
                        <TableCell 
                          key={column.id.toString()} 
                          align={column.align}
                          sx={{ 
                            fontSize: '0.75rem',
                            py: 1,
                            color: 'inherit',
                            cursor: column.clickable ? 'pointer' : (column.text ? 'text' : 'default'),
                            backgroundColor: column.clickable ? '#cce5f7ff' : 'none'
                          }}
                          onClick={() => {
                              handleCellClick(column, row);
                              onDopRabotaChange(row);
                            if (column.text) {
                              setEditCell({rowKey, columnId: column.id.toString()});
                            }
                          }}
                          onDoubleClick={()=>{
                            handleDoubleClick(column,row);
                          }}
                        >
                          
                          {/* Для форматирования в ячейке */}
                          {isEditing ? (
                            <TextField
                              value={value || ""}
                              onChange={(e) =>
                                handleCellChange(row, rowKey, column.id.toString(), e.target.value, column.text)
                              }
                              onBlur={() => {
                                setEditCell(null);
                                handleCellBlur(rowKey, column.id.toString());
                              }}
                              autoFocus
                              size="small"
                              variant="standard"
                              InputProps={{
                                disableUnderline: true,
                                sx: {
                                  fontSize: "0.7rem",
                                  padding: 0,
                                  margin: 0,
                                  "& input": {
                                    padding: 0,
                                    margin: 0,
                                    textAlign: column.align || "left", // сохраняем выравнивание
                                  },
                                },
                              }}
                              sx={{ width: "100%", background: "transparent" }}
                            />
                          ) : column.format && typeof value === "number" ? (
                            column.format(value)
                          ) : (
                            value || "-"
                          )}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      
      <TablePagination
        rowsPerPageOptions={[10, 25, 50, 100]}
        component="div"
        count={localData.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
        labelRowsPerPage="Строк на странице:"
        labelDisplayedRows={({ from, to, count }) => 
          `${from}-${to} из ${count !== -1 ? count : `более ${to}`}`
        }
      />

      {selectedRabotaForDialog && isMaterialDopOpen && (
        <MaterialDop 
          rabota={selectedRabotaForDialog}
          nameOfObject={nameOfObject!} 
          flagTipRab="Мелкая"
          open={isMaterialDopOpen}
          onClose={handleCloseMaterialDop}
        />
    )}

    {isMaterialOpen && selectedRabotaForDialog && (
        <Material
          rabota={selectedRabotaForDialog}
          nameOfObject={nameOfObject!} 
          flagTipRab="Мелкая"
          open={isMaterialOpen}
          onClose={handleCloseMaterial}
          etaj ={activeEtaj}
          etajValue= {activeEtajValue}
        />
    )}
      
    </Box>
  );
}

export default DopRabotaTable;